import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(
    private http: HttpClient
  ) { }

  login(data) {
    return this.http.post(`${environment.endpoint}/login`, data).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  loginSocial(data) {
    return this.http.post(`${environment.endpoint}/loginSocial`, data).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  register(body) {
    return this.http.post(`${environment.endpoint}/registerUser`, body).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  updateProfile(body) {
    const headers = this.getHeaders();
    return this.http.post(`${environment.endpoint}/profile/update/info`, body, { headers }).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  updatePassword(body) {
    const headers = this.getHeaders();
    return this.http.post(`${environment.endpoint}/profile/update/password`, body, { headers }).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  updatePhone(phone){
    const userId = JSON.parse(localStorage.getItem('user')).id;
    const body = { userId, phone }
    const headers = this.getHeaders();
    return this.http.post(`${environment.endpoint}/update-phone`, body, { headers }).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getUser(id) {
    const headers = this.getHeaders();
    return this.http.get(`${environment.endpoint}/profile/get?profileId=${id}`, { headers }).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  recoverPassword(body) {
    const headers = this.getHeaders();
    return this.http.post(`${environment.endpoint}/reset-password`, body, { headers }).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getHeaders() {
    var getToken = JSON.parse(localStorage.getItem('token'));
    var token = "Bearer " + getToken;
    const headers = new HttpHeaders({ 'Authorization': token });
    return headers;
  }

}
