import { Component, OnInit } from '@angular/core';
import { SocialAuthService } from "angularx-social-login";
import { RegisterClass } from 'src/app/models/user.models';
import { UserService } from 'src/app/services/user.service';
import { FacebookLoginProvider, GoogleLoginProvider } from "angularx-social-login";
import { SocialUser } from "angularx-social-login";
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  user: SocialUser;
  loggedIn: boolean;
  userRegister = new RegisterClass('', '', '', '', '');
  public googleLogin = {
    id: '',
    email: ''
  };
  public loading = false;

  constructor(
    private authService: SocialAuthService,
    private userService: UserService,
    private toastr: ToastrService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.authService.authState.subscribe((user) => {
      var url = this.router.url;
      const split = url.split('/');
      if (split[1] === "registrarse") {
        this.userRegister.name = user.name;
        this.userRegister.email = user.email;
        this.register(user.provider);
      }
    });

    const setInterval = JSON.parse(localStorage.getItem('setIntervals'));
    clearInterval(setInterval);
    setTimeout(() => {
      localStorage.removeItem('setIntervals')
    }, 200);
  }

  signInWithGoogle(): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID);
    this.userRegister.type = "GOOGLE";
  }

  signInWithFB(): void {
    this.authService.signIn(FacebookLoginProvider.PROVIDER_ID);
    this.userRegister.type = "FACEBOOK";
  }

  signOut(): void {
    this.authService.signOut();
  }

  register(type = 'REGISTER-WEB') {
    this.loading = true;
    const name = this.userRegister.name;
    const email = this.userRegister.email;
    const phone = this.userRegister.mobile_number;
    const password = this.userRegister.password;
    this.userRegister.type = type;
    if (type === "REGISTER-WEB") {
      if (name === "" || email === "" || phone === "" || password === "") {
        setTimeout(() => {
          this.loading = false;
          this.toastr.info('Por favor complete los campos');
        }, 800);
      } else {
        this.userService.register(this.userRegister).subscribe(
          (res) => {
            if (res.status) {
              if (res.code === '200') {
                this.toastr.success('Usuario creado satisfactoriamente');
                const auth = { email, password, role : 'customer' }
                this.userService.login(auth).subscribe(
                  (data) => {
                    if(data.token) {
                      var token = data.token;
                      var user = data.user;
                      localStorage.setItem('token', JSON.stringify(token));
                      localStorage.setItem('user', JSON.stringify(user));
                      this.toastr.success('Logueo satisfactoriamente');
                      if (JSON.parse(sessionStorage.getItem('SESSION_ROUTE'))) {
                        sessionStorage.removeItem('SESSION_ROUTE');
                        setTimeout(() => {
                          window.location.href = "#/procesar-compra";
                        }, 1000);
                      } else {
                        setTimeout(() => {
                          window.location.href = "#/inicio";
                        }, 1000);
                      }
                    } else {
                      setTimeout(() => {
                        this.loading = false;
                        this.toastr.info('Credenciales incorrectas');
                      }, 800);
                    }
                  }
                );
              } else {
                this.loading = false;
                this.toastr.info('Esta cuenta ya está registrado, intente con otra cuenta.');
              }
            } else {
              this.toastr.error('Error al momento de registrarse, por favor comuníquese con nosotros para ayudarte');
            }
          },
          (error) => {
            this.toastr.error('Error al momento de registrarse, por favor comuníquese con nosotros para ayudarte');
          }
        );
      }
    } else {
      if (name === "" || email === "") {
        this.toastr.info('Por favor complete los campos');
      } else {
        this.userService.register(this.userRegister).subscribe(
          (res) => {
            if (res.status) {
              if (res.code === '200') {
                this.toastr.success('Usuario creado satisfactoriamente');
                const auth = { email, role : 'customer' }
                this.userService.loginSocial(auth).subscribe(
                  (data) => {
                    if(data.token) {
                      var token = data.token;
                      var user = data.user;
                      localStorage.setItem('token', JSON.stringify(token));
                      localStorage.setItem('user', JSON.stringify(user));
                      this.toastr.success('Logueo satisfactoriamente');
                      if (JSON.parse(sessionStorage.getItem('SESSION_ROUTE'))) {
                        sessionStorage.removeItem('SESSION_ROUTE');
                        setTimeout(() => {
                          window.location.href = "#/procesar-compra";
                        }, 1000);
                      } else {
                        setTimeout(() => {
                          window.location.href = "#/mi-perfil";
                        }, 1000);
                      }
                    } else {
                      setTimeout(() => {
                        this.loading = false;
                        this.toastr.info('Credenciales incorrectas');
                      }, 800);
                    }
                  }
                );
              } else {
                this.userRegister = new RegisterClass('', '', '', '', '');
                this.loading = false;
                this.toastr.info('Esta cuenta ya está registrado, puede iniciar sesión');
              }
            } else {
              this.toastr.error('Error al momento de registrarse, por favor comuníquese con nosotros para ayudarte');
            }
          },
          (error) => {
            this.toastr.error('Error al momento de registrarse, por favor comuníquese con nosotros para ayudarte');
          }
        );
      }
    }
  }

  maxNumbersPhone(e) {
    var phone = e.target['value'];
    if (phone.length > 8) {
      return false;

    } else {
      return '';
    }
  }

}
