import { Component, OnInit, AfterViewInit } from '@angular/core';
import { OrdersCreateClass } from 'src/app/models/order.models';
import { UserClass } from 'src/app/models/user.models';
import { OrdersService } from 'src/app/services/orders.service';
import { SucursalService } from 'src/app/services/sucursal.service';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { BoardService } from 'src/app/services/board.service';
import { ToastrService } from 'ngx-toastr';
import { CoordenatesClass } from 'src/app/models/checkout.models';
import "leaflet/dist/leaflet.css";
import "leaflet-draw";
import "leaflet-draw/dist/leaflet.draw.css";
import * as GeoSearch from 'leaflet-geosearch';
import { MapsAPILoader } from '@agm/core';
import Swal from 'sweetalert2';
import { CouponsService } from 'src/app/services/coupons.service';
import { StoresOpen } from 'src/app/models/stores.models';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

interface HtmlInputEvent extends Event {
  target: HTMLInputElement & EventTarget;
}

declare const L: any;
declare var google: any;
@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements AfterViewInit {

  public storesHours = new StoresOpen(0);
  orders = new OrdersCreateClass('', {}, 0, [], 0, '', '1', '', '', '', '', '0.00', false, '', 0, '', '', '', '', '');
  coordenates = new CoordenatesClass('', '', '', '');
  public user = new UserClass('', '', '', '', '', '', '', '', '', '', '', '', '', '', 0, 0, [], 'GOOGLE', '', 0);
  public getCarts = [];
  public totalPrincipal;
  public suma = 0;
  public NewTotal = [];
  public igv;
  public subTotal;
  public igvDecimal;
  public sucursal;
  public methodPaymentId = 0;
  public data = {
    "email": "",
    "password": "",
    "role": "customer"
  }
  public sucursalGet = [];
  public validateMap = false;
  public token;
  public userId = "";
  public logueo = false;
  public validateEmail = false;
  public usuarios = false;
  adress; map; lat; lng; latitud; longitude; drawnItems;
  public coordPolygon = [];
  public coordenatesGet = [];
  public validatePages = 0;
  file: File;
  public loading = false;
  public cupone: string;
  public validate = false;
  public descuentoBoolean = false;
  public discount = 0;
  public description: string;
  public priceDelivery = 0;
  public waiter = false;
  public boards = [];
  maps: google.maps.Map;
  marker;
  public clickMarker = false;
  public validateLocationCobertura = false;
  public loadingMap = true;
  public codeCoupon: string;
  public sumaBack: number;
  public nameUser: string;
  public loadingInit = true;
  public sucursalRequest = {
    "yapeNumber": "",
    "yapeQR": "",
    "plinNumber": "",
    "plinQR": ""
  }
  public tokenUser: String;
  public opennig = true;
  public totalCesta = 0;
  public initValue = false;
  public namePlinImage = "";
  public nameYapeImage = "";
  public image = { checkout : '' }
  public buttonOrder = false;


  constructor(
    private orderService: OrdersService,
    private router: Router,
    private userService: UserService,
    private toastr: ToastrService,
    private sucursalService: SucursalService,
    private boardService: BoardService,
    private couponService: CouponsService,
    private settingService : SettingService
  ) {
    const sucursal = JSON.parse(localStorage.getItem('sucursal'));
    this.sucursal = sucursal.sucursal;
  }

  getLocation() {
    navigator.geolocation.getCurrentPosition(
      (pos) => {
        var coor = pos;
        this.lat = coor.coords.latitude;
        this.lng = coor.coords.longitude;
        //this.markerMap();
      }
    )
  }

  async ngOnInit() {
    const bannerStorage = JSON.parse(localStorage.getItem('bannerCheckout'));
    if (bannerStorage) {
      this.image.checkout = bannerStorage;
    } else {
      this.image.checkout = `"assets/img/prods/1920x500/waffleduonaranja.png"`;
    }
    document.getElementById("banner").style.backgroundImage = `url(${this.image.checkout})`;
    this.getSettingsImage();
    await this.getSucursal();
    this.validateHours();
    this.getUsers();
    const description = JSON.parse(localStorage.getItem("description"));
    const token = JSON.parse(localStorage.getItem('token'));
    this.tokenUser = token;
    this.token = token;
    if (token) {
      const userSession = JSON.parse(localStorage.getItem('user'));
      this.nameUser = userSession.name;
      this.usuarios = true;
      this.getTypeOrder();
    } else {
      this.toastr.info('Debe iniciar sesión para procesar su orden');
      this.loadingInit = false;
    }
    if (description) {
      this.orders.description = description;
    }

    const setInterval = JSON.parse(localStorage.getItem('setIntervals'));

    this.orders.coupon = "";

    clearInterval(setInterval);
    setTimeout(() => {
      localStorage.removeItem('setIntervals')
    }, 200);
  }

  getSettingsImage() {
    this.settingService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const checkout = resp.find(e => e.id == 7);
        if (checkout) {
          const checkoutURI = `${environment.store}/${checkout.upload}`;
          localStorage.setItem('bannerCheckout', JSON.stringify(checkoutURI));
          this.image.checkout = checkoutURI;
        } else {
          localStorage.setItem('bannerCheckout', `"assets/img/prods/1920x500/waffleduonaranja.png"`);
          this.image.checkout = `"assets/img/prods/1920x500/waffleduonaranja.png"`;
        }
        document.getElementById("banner").style.backgroundImage = `url(${this.image.checkout})`;
      }
    );
  }

  validateHours() {
    this.storesHours.storeId = JSON.parse(localStorage.getItem('sucursal')).sucursal;
    this.sucursalService.validateSucursal().subscribe(
      (res) => {
        const message = res.data[0].message;
        if (message == 'OPEN') {
          this.opennig = true;
        } else {
          this.opennig = false;
          this.router.navigate(['/cesta']);
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }

  ngAfterViewInit() {
    //this.getLocation();
    this.getCoordenates();
    //this.initializeMap();
  }

  markerMap(coordenatesGet) {
    if (!this.waiter) {
      setTimeout(() => {
        var map = L.map('map').setView([-12.197380, -76.994067], 12);
        L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
          //attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
          maxZoom: 15
        }).addTo(map);

        const search = new GeoSearch.GeoSearchControl({
          provider: new GeoSearch.OpenStreetMapProvider(),
        });
        map.addControl(search);
        var coordenates = [];
        for (let i = 0; i < coordenatesGet.length; i++) {
          coordenates.push([parseFloat(coordenatesGet[i].latitude), parseFloat(coordenatesGet[i].longitude)]);
        }
        var polygon = L.polygon(coordenates).addTo(map);
        var popupPolygon = L.popup();
        //polygon.bindPopup(popupPolygon).openPopup();
        polygon.on('click', (e) => {
          if (this.lat === "" || this.lng === "" || this.lat === undefined || this.lng === undefined) {
            this.lat = e.latlng.lat;
            this.lng = e.latlng.lng;
            var customIcon = new L.Icon({
              iconUrl: 'https://i.pinimg.com/originals/25/62/aa/2562aacd1a4c2af60cce9629b1e05cf2.png',
              iconSize: [30, 30],
              iconAnchor: [20, 30]
            });
            L.marker([this.lat, this.lng], { icon: customIcon }).addTo(map);
          } else {
            document.getElementsByClassName("leaflet-marker-icon")[0].remove();
            setTimeout(() => {
              var customIcon = new L.Icon({
                iconUrl: 'https://i.pinimg.com/originals/25/62/aa/2562aacd1a4c2af60cce9629b1e05cf2.png',
                iconSize: [30, 30],
                iconAnchor: [15, 25]
              });
              this.lat = e.latlng.lat;
              this.lng = e.latlng.lng;
              L.marker([this.lat, this.lng], { icon: customIcon }).addTo(map);
            }, 200)
          }
        });
      }, 500);
    }
  }

  getCart() {
    const menuCheckout = JSON.parse(localStorage.getItem('menuCheckout'));
    if (menuCheckout) {
      this.getCarts = menuCheckout;
      for (let i = 0; i < this.getCarts.length; i++) {
        var arrryPrice = [];
        var suma = 0;
        if (this.getCarts[i].complement) {
          var group = this.getCarts[i].complement;
          for (let j = 0; j < group.length; j++) {
            var complement = group[j].complement;
            for (let k = 0; k < complement.length; k++) {
              var price = complement[k].price;
              arrryPrice.push(parseFloat(price));
            }
          }
          arrryPrice.forEach(function (numero) {
            suma += numero;
          });
        }
        this.getCarts[i].totalComplement = suma;
        this.totalPrincipal = this.getCarts[i].menu.price * this.getCarts[i].quantity;
        const parsePriceDelivery = this.priceDelivery;
        this.suma += (this.getCarts[i].menu.price * this.getCarts[i].quantity) + (suma * this.getCarts[i].quantity);
      }
      this.sumaBack = this.suma;
    }
  }

  getPriceOrder() {
    if (this.waiter) {
      this.orders.typeOrderId = '3';
      const sumDelivery = this.suma;
      const subTotal = sumDelivery / 1.18;
      this.subTotal = Number((subTotal).toFixed(2));
      this.orders.subtotal = this.subTotal;
      this.igv = sumDelivery - this.subTotal;
      this.igvDecimal = this.igv.toFixed(2);
      this.orders.igv = this.igvDecimal;
      this.orders.discount = "0";
    } else {
      if (this.orders.typeOrderId === '1') {
        this.ngAfterViewInit();
        if (this.initValue) {
          this.suma = this.suma;
        } else {
          this.suma = this.suma + this.priceDelivery;
        }
        const sumDelivery = this.suma;
        const subTotal = sumDelivery / 1.18;
        this.subTotal = Number((subTotal).toFixed(2));
        this.orders.subtotal = this.subTotal;
        this.igv = sumDelivery - this.subTotal;
        this.igvDecimal = this.igv.toFixed(2);
        this.orders.igv = this.igvDecimal;
        this.orders.discount = "0";
      } else {
        this.descuentoBoolean = false;
        this.validate = false;
        this.suma = this.sumaBack;
        const sumDelivery = this.suma;
        const subTotal = sumDelivery / 1.18;
        this.subTotal = Number((subTotal).toFixed(2));
        this.orders.subtotal = this.subTotal;
        this.igv = sumDelivery - this.subTotal;
        this.igvDecimal = this.igv.toFixed(2);
        this.orders.igv = this.igvDecimal;
        this.orders.discount = "0";
      }
    }
  }

  login() {
    this.logueo = false;
    var regex = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
    if (regex.test(this.data.email) == false) {
      this.validateEmail = true;
    } else {
      this.validateEmail = false;
    }
    if (this.validateEmail == false) {
      this.userService.login(this.data).subscribe(
        (data) => {
          var token = data.token;
          var user = data.user;
          localStorage.setItem('token', JSON.stringify(token));
          localStorage.setItem('user', JSON.stringify(user));
          this.toastr.success('Logueo satisfactoriamente');
          this.ngOnInit();
          this.logueo = true;
        },
        (error) => {
          this.toastr.warning('Error en las credenciales');
        }
      )
    }
  }

  createOrder() {
    this.readCookie();

    if (document.getElementById('searchInput')) {
      //@ts-ignore
      const address = document.getElementById('searchInput').value;
      this.coordenates.address = address;
    }

    this.coordenates.title = "Dirección de entrega";
    this.coordenates.latitude = this.lat;
    this.coordenates.longitude = this.lng;
    const user = JSON.parse(localStorage.getItem('user'));
    if (user) {
      this.loading = true;
      const checkout = JSON.parse(localStorage.getItem('menuCheckout'));
      this.userId = user.id;
      const getToken = localStorage.getItem('token');

      // Validate token
      if (!getToken || getToken === null) {
        this.toastr.info('Debe iniciar sesión');
        this.loading = false;
      } else if (this.user.mobile_number === "" || this.user.mobile_number === null) {
        this.loading = false;
        this.toastr.info('Por favor actualice su número telefónico');
      } else {

        // If is waiter convert in true
        if (this.waiter) {
          this.orders.typeOrderId = '3';
        }
        this.orders.userId = this.userId;
        this.orders.checkout = checkout;
        this.orders.methodPaymentId = this.methodPaymentId;
        this.orders.waiter = this.waiter;
        this.orders.priceTotal = this.totalCesta;

        const covered = JSON.parse(localStorage.getItem('covered'));
        this.orders.covered = covered;
        this.orders.sucursalId = this.sucursal;

        // Validate when the order is '1'
        if (this.orders.typeOrderId === '1') {
          if (
            this.coordenates.latitude === undefined ||
            this.coordenates.longitude === undefined
          ) {
            this.loading = false;
            this.toastr.info('Por favor seleccione sus puntos de coordenadas');
          }
          this.orders.coordinates = this.coordenates;
        }
        if (this.orders.methodPaymentId === 0) {
          this.loading = false;
          this.toastr.info('Por favor seleccione el método de pago');
        } else {
          const tpyeId = this.orders.typeOrderId;
          if (!this.waiter) {
            if (this.orders.typeOrderId === '1') {
              if (this.adress === "" || this.adress === undefined || this.adress === null) {
                this.loading = false;
                this.adress = this.orders.addressExact;
              }
              if (this.orders.reference === "" || this.orders.addressExact === "") {
                this.loading = false;
                this.toastr.info('Ingrese una dirección y/o referencia');
              } else {
                if (this.methodPaymentId === 3) {
                  if (parseFloat(this.orders.pago_con) >= this.suma) {
                    if (this.validateLocationCobertura) {
                      this.serviceCreateOrder();
                    } else {
                      this.loading = false;
                      Swal.fire(
                        'Opps...',
                        'Al parecer se encuentra fuera de cobertura, por favor inténtelo nuevamente',
                        'error'
                      );
                    }
                  } else {
                    this.loading = false;
                    this.toastr.info('Ingresar un pago mayor al monto total');
                  }
                } else {
                  if (this.validateLocationCobertura) {
                    this.serviceCreateOrder();
                  } else {
                    this.loading = false;
                    Swal.fire(
                      'Opps...',
                      'Al parecer se encuentra fuera de cobertura, por favor inténtelo nuevamente',
                      'error'
                    );
                  }
                }

              }
            } else {
              this.serviceCreateOrder();
            }
          } else {
            this.serviceCreateOrder();
          }

        }
      }
    }
  }

  serviceCreateOrder() {
    if (this.opennig) {
      if (this.user.mobile_number.length === 9) {
        if (this.methodPaymentId === 1 || this.methodPaymentId === 2 || this.methodPaymentId === 5) {
          if (this.file != undefined) {
            const promise = new Promise((resolve, reject) => {
              this.userService.updatePhone(this.user.mobile_number).subscribe(
                res => { resolve(res) },
                err => { reject(err) }
              );
            });
            this.orderService.createOrder(this.orders).subscribe(
              (res) => {
                if (res.status) {
                  const orderId = res.data.orderId;
                  setTimeout(() => {
                    if (this.methodPaymentId === 1 || this.methodPaymentId === 2 || this.methodPaymentId === 5) {
                      this.orderService.uploadsFile(orderId, this.file).subscribe(
                        (data) => {
                          this.toastr.success('Orden Creada satisfactoriamente');
                          //@ts-ignore
                          if (data.status) {
                            setTimeout(() => {
                              this.openModal();
                              setTimeout(() => {
                                localStorage.removeItem('menuCheckout');
                                localStorage.removeItem('description');
                                this.router.navigate(['mis-ordenes']);
                              }, 6000);
                            }, 700);
                          }
                        }
                      );
                    } else {
                      //this.toastr.success('Orden Creada satisfactoriamente');
                      setTimeout(() => {
                        this.openModal();
                        setTimeout(() => {
                          localStorage.removeItem('menuCheckout');
                          this.router.navigate(['mis-ordenes']);
                        }, 6000);
                      }, 700);
                    }
                  }, 500);
                } else {
                  if (res.message === "Phone not found") {
                    this.toastr.info('Agregue un número de celular');
                  }
                  if (res.message === "Price not valid") {
                    this.toastr.info('El precio enviado no es permitido, por favor valide su precio para procesar su pedido');
                  }
                  this.toastr.warning('Hubo un error en su proceso de orden, por favor inténtelo nuevamente');
                }
              },
              (error) => {
                this.toastr.error('Hubo un error, por favor comuníquese con nosotros para ayudarlo.');
              }
            );
          } else {
            this.loading = false;
            this.toastr.warning('Debe adjuntar su comprobante');
          }
        } else {
          const promise = new Promise((resolve, reject) => {
            this.userService.updatePhone(this.user.mobile_number).subscribe(
              res => { resolve(res) },
              err => { reject(err) }
            );
          });
          this.buttonOrder = true;
          this.orderService.createOrder(this.orders).subscribe(
            (res) => {
              if (res.status) {
                setTimeout(() => {
                  this.openModal();
                  setTimeout(() => {
                    this.buttonOrder = false;
                    localStorage.removeItem('menuCheckout');
                    this.router.navigate(['mis-ordenes']);
                  }, 6000);
                }, 700);
              } else {
                if (res.message === "Phone not found") {
                  this.toastr.info('Agregue un número de celular');
                }
                if (res.message === "Price not valid") {
                  this.toastr.info('El precio enviado no es permitido, por favor valide su precio para procesar su pedido');
                }
                this.buttonOrder = false;
                this.toastr.warning('Hubo un error en su proceso de orden, por favor inténtelo nuevamente');
              }
            },
            (error) => {
              this.buttonOrder = false;
              this.toastr.error('Al parecer ha sucedido un error, por favor comuníquese con nosotros para ayudarlo.');
            }
          );
        }
      } else {
        this.toastr.error('Escriba un número de celular válido.');
      }
    } else {
      Swal.fire(
        'Opps...',
        'La tienda se encuentra cerrada',
        'info'
      );
    }
  }

  readCookie() {
    let cookies = document.cookie;
    let getCookies = cookies.split(';');
    let search;
    for (let i in getCookies) {
      search = getCookies[i].split('=');
      if (search[0] === " _total" || search[0] === "_total") {
        this.totalCesta = parseInt(atob(search[1]));
      }
    }
  }

  validarInputs() {
    var flag = true;
    for (var _i = 0; _i < 2; _i++) {
      var textInput = <HTMLInputElement>document.getElementsByClassName("required-text")[_i];
      if (textInput.value === "" || textInput.value === "0") {
        textInput.classList.add("is-invalid");
        flag = false;
      } else {
        textInput.classList.remove("is-invalid");
      }
    }
    return flag;
  }

  getUsers() {
    const user = JSON.parse(localStorage.getItem('user'));
    if (user) {
      //this.user = user;
      this.userService.getUser(user.id).subscribe(
        (res) => {
          this.user = res.data[0];
          if (this.user.role_id === 6) {
            this.waiter = true;
            //this.getPriceOrder();
            this.getBoard();
          } else {
            //this.getPriceOrder();
          }
          this.loadingInit = false;
        }
      );
    }
  }

  getCoordenates() {
    this.sucursalService.getCoordenates().subscribe(
      (res) => {
        if (res.status) {
          this.coordenatesGet = res.data;
          this.initializeMap(this.coordenatesGet);
        }
      }
    );
  }

  onPhotoSelected(event: HtmlInputEvent): void {
    if (event.target.files && event.target.files[0]) {
      this.file = <File>event.target.files[0];
      if (this.validatePages === 1) {
        this.namePlinImage = "";
        this.nameYapeImage = this.file.name;
      } else if (this.validatePages === 2) {
        this.namePlinImage = this.file.name;
        this.nameYapeImage = "";
      }
    }
  }

  getValidatePages() {
    this.namePlinImage = "";
    this.nameYapeImage = "";
    //@ts-ignore
    const validateNumber = document.getElementById('validatePages').value;
    this.validatePages = parseInt(validateNumber);
    this.methodPaymentId = this.validatePages;
    const sucursal = JSON.parse(localStorage.getItem('sucursal'));
    if (sucursal) {
      const sucursalId = sucursal.sucursal;
      const sucursalStorage = this.sucursalGet.filter((e) => e.id == sucursalId);
      const objectSucursal = sucursalStorage[0];
      this.sucursalRequest.yapeNumber = objectSucursal.yapeNumber;
      this.sucursalRequest.yapeQR = objectSucursal.yapeQR;
      this.sucursalRequest.plinNumber = objectSucursal.plinNumber;
      this.sucursalRequest.plinQR = objectSucursal.plinQR;
    }
  }

  getTypeOrder() {
    this.loading = true;
    this.loadingMap = true;
    setTimeout(() => {
      //@ts-ignore
      const typeOrder = document.getElementById('typeOrder').value;
      this.orders.typeOrderId = typeOrder;
      this.getPriceOrder();
      if (typeOrder == '1') {
        this.ngAfterViewInit();
      }
      this.loading = false;
    }, 1000);
  }

  applyCode() {
    this.loading = true;
    this.descuentoBoolean = true;
    const json = { coupon: this.codeCoupon };
    this.couponService.getCoupon(json).subscribe(
      (res) => {
        if (res.status) {
          const resp = res.data[0];
          if (resp) {
            const expired = resp.expires_at;
            var fecha = expired.split("-");
            var x = new Date(fecha[0], fecha[1] - 1, fecha[2]);
            var today = new Date();
            if (x <= today) {
              this.loading = false;
              this.descuentoBoolean = false;
              this.toastr.info('Cupón vencido, por favor digíte otro');
            }
            else {
              this.orders.coupon = this.codeCoupon;
              if (resp.type === 'percent') {
                const reward = parseInt(resp.reward);
                this.orders.coupon_code = this.codeCoupon;
                setTimeout(() => {
                  this.suma = this.suma;
                  this.discount = Number((this.suma * (reward / 100)).toFixed(2));
                  this.suma = this.suma - this.discount;
                  const subtotal = Number((this.suma / 1.18).toFixed(2));
                  this.orders.subtotal = subtotal.toString();
                  const igv = Number((this.suma - subtotal).toFixed(2));
                  this.orders.igv = igv.toString();
                  const discount = this.discount;
                  this.orders.discount = discount.toString();
                  this.loading = false;
                  this.validate = true;
                }, 1000);
              }
              if (resp.type === 'fixed') {
                const reward = parseInt(resp.reward);
                this.orders.coupon_code = this.codeCoupon;
                setTimeout(() => {
                  this.suma = this.suma;
                  this.discount = Number((reward).toFixed(2));
                  this.suma = this.suma - this.discount;
                  const subtotal = Number((this.suma / 1.18).toFixed(2));
                  this.orders.subtotal = subtotal.toString();
                  const igv = Number((this.suma - subtotal).toFixed(2));
                  this.orders.igv = igv.toString();
                  const discount = this.discount;
                  this.orders.discount = discount.toString();
                  this.loading = false;
                  this.validate = true;
                }, 1000);
              }
            }
          } else {
            this.toastr.info('Cupón incorrecto, por favor digíte otro');
            this.descuentoBoolean = false;
            this.loading = false;
          }
        }
      }
    )
  }

  openModal() {
    let modal = document.getElementById('modal');
    modal.style.display = "block";
  }

  closeModal() {
    let modal = document.getElementById('modal');
    modal.style.display = "none";
  }

  getSucursal() {
    return new Promise((resolve, reject) => {
      const sucursal = JSON.parse(localStorage.getItem('sucursal'));
      this.sucursalService.getSucursal().subscribe(
        (res) => {
          if (res.status) {
            if (this.tokenUser) {
              if (sucursal) {
                const sucursalId = sucursal.sucursal;
                const data = res.data;
                this.sucursalGet = data;
                const sucursalFilter = data.filter((e) => e.id == sucursalId);
                this.priceDelivery = parseFloat(sucursalFilter[0].delivery_fee);
                this.getCart();
              }
              resolve(true);
            } else {
              const sucursalId = sucursal.sucursal;
              const data = res.data;
              this.sucursalGet = data;
              const sucursalFilter = data.filter((e) => e.id == sucursalId);
              this.priceDelivery = parseFloat(sucursalFilter[0].delivery_fee);
              this.getCart();
              resolve(true);
            }
          }
        }
      );
    })
  }

  getBoard() {
    this.boardService.getCategory().subscribe(
      (res) => {
        if (res.status) {
          this.boards = res.data;
        }
      }
    );
  }

  selectedBoard(boardId) {
    this.orders.board = boardId;
  }

  initializeMap(coordenatesGet) {
    if (this.loadingMap) {
      var arrayCoordenates = [];
      for (let i = 0; i < coordenatesGet.length; i++) {
        const jsonCoordenates = { lat: parseFloat(coordenatesGet[i].latitude), lng: parseFloat(coordenatesGet[i].longitude) };
        arrayCoordenates.push(jsonCoordenates);
      }
      var options;
      options = { zoom: 14, center: { lat: arrayCoordenates[0].lat, lng: arrayCoordenates[0].lng } }
      this.maps = new google.maps.Map(document.getElementById('map'), options);
      google.maps.event.addListener(this.maps, 'click', (event) => {
        const latitude = event.latLng.lat();
        const longitude = event.latLng.lng();
        this.lat = latitude;
        this.lng = longitude;
        const coordenates = { lat: latitude, lng: longitude };
        if (this.clickMarker) {
          this.cleanMarker();
        }
        this.addMarker({ coords: coordenates });
        if (!this.clickMarker) {
          this.clickMarker = true;
        }

        setTimeout(() => {
          var rsul = google.maps.geometry.poly.containsLocation(new google.maps.LatLng(latitude, longitude), bermudaTriangle);
          if (rsul) {
            this.validateLocationCobertura = true;
          } else {
            this.validateLocationCobertura = false;
          }
        }, 500);
      });

      // Define the LatLng coordinates for the polygon's path.
      /*const triangleCoords = [
        { lat: -12.164290627803096, lng: -77.00796317949425 },
        { lat: -12.191406877558233, lng: -77.02512931718957 },
        { lat: -12.196507643382839, lng: -77.01819419336971 },
        { lat: -12.180735218072618, lng: -77.00411796045957 },
        { lat: -12.167244017989725, lng: -77.00322532444264 },
      ];*/
      const triangleCoords = arrayCoordenates;

      // Construcción de polygons
      const bermudaTriangle = new google.maps.Polygon({
        paths: triangleCoords,
        strokeColor: "#FF0000",
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: "#FF0000",
        fillOpacity: 0.35,
      });

      bermudaTriangle.setMap(this.maps);

      google.maps.event.addListener(bermudaTriangle, 'click', (event) => {
        const latitude = event.latLng.lat();
        const longitude = event.latLng.lng();
        this.lat = latitude;
        this.lng = longitude;
        const coordenates = { lat: latitude, lng: longitude };
        if (this.clickMarker) {
          this.cleanMarker();
        }
        this.addMarker({ coords: coordenates });
        if (!this.clickMarker) {
          this.clickMarker = true;
        }

        setTimeout(() => {
          var rsul = google.maps.geometry.poly.containsLocation(new google.maps.LatLng(latitude, longitude), bermudaTriangle);
          if (rsul) {
            this.validateLocationCobertura = true;
          } else {
            this.validateLocationCobertura = false;
          }
        }, 500);
      });

      // Dentro de la cobertura
      // -12.1824591, -77.01156019999999
      // Fuera de la cobertura
      // -12.1861441, -77.0013515

      // Search Filtro

      var input = document.getElementById('searchInput');

      const searchBox = new google.maps.places.SearchBox(input);
      this.maps.controls[google.maps.ControlPosition.TOP_LEFT].push(input);

      this.maps.addListener("bounds_changed", () => {
        searchBox.setBounds(this.maps.getBounds() as google.maps.LatLngBounds);
      });

      let markerss: google.maps.Marker[] = [];
      searchBox.addListener("places_changed", () => {
        const places = searchBox.getPlaces();

        if (places.length == 0) {
          return;
        }

        // Clear out the old markers.
        markerss.forEach((marker) => {
          marker.setMap(null);
        });
        markerss = [];

        const bounds = new google.maps.LatLngBounds();
        places.forEach((place) => {
          if (this.clickMarker) {
            this.cleanMarker();
          }
          if (!this.clickMarker) {
            this.clickMarker = true;
          }
          if (!place.geometry || !place.geometry.location) {
            //console.log("Returned place contains no geometry");
            return;
          }
          const icon = {
            url: place.icon as string,
            size: new google.maps.Size(71, 71),
            origin: new google.maps.Point(0, 0),
            anchor: new google.maps.Point(15, 15),
            scaledSize: new google.maps.Size(30, 30),
          };

          //console.log(place.geometry.location.lat());
          //console.log(place.geometry.location.lng());
          this.lat = place.geometry.location.lat();
          this.lng = place.geometry.location.lng();

          setTimeout(() => {
            var rsul = google.maps.geometry.poly.containsLocation(new google.maps.LatLng(place.geometry.location.lat(), place.geometry.location.lng()), bermudaTriangle);
            if (rsul) {
              this.validateLocationCobertura = true;
            } else {
              this.validateLocationCobertura = false;
            }
          }, 500);

          this.marker = new google.maps.Marker({
            map: this.maps,
            title: place.name,
            position: place.geometry.location,
          });

          /*markerss.push(
            new google.maps.Marker({
              map: this.maps,
              title: place.name,
              position: place.geometry.location,
            })
          );*/

          if (place.geometry.viewport) {
            bounds.union(place.geometry.viewport);
          } else {
            bounds.extend(place.geometry.location);
          }
        });
        this.maps.fitBounds(bounds);
      });
      this.loadingMap = false;
    }
  }

  addMarker(props) {

    this.marker = new google.maps.Marker({
      position: props.coords,
      map: this.maps,
      icon: props.iconImage
    });

    this.marker.setIcon();

    if (props.iconImage) {
      this.marker.setIcon(props.iconImage);
    }
  }

  cleanMarker() {
    this.marker.setMap(null);
  }

  onlyNumber(e) {
    var key = window.event ? e.which : e.keyCode;
    if (key < 48 || key > 57) {
      e.preventDefault();
    }

    var phone = e.target['value'];
    if (phone.length > 8) {
      return false;

    } else {
      return '';
    }
  }

  register() {
    this.router.navigate(['/registrarse']);
  }

  singin() {
    this.router.navigate(['/iniciar-sesion']);
  }

  copy(phone) {
    this.toastr.success('Texto copiado');
    const aux = document.createElement("input");
    aux.setAttribute("value", phone);
    document.body.appendChild(aux);
    aux.select();
    document.execCommand("copy");
    document.body.removeChild(aux);
  }

}
