import { Component, OnInit } from '@angular/core';
import { SucursalService } from 'src/app/services/sucursal.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-home-layouts',
  templateUrl: './home-layouts.component.html',
  styleUrls: ['./home-layouts.component.css']
})
export class HomeLayoutsComponent implements OnInit {

  sucursal = [];
  sucursalId = 0;

  constructor(
    private sucursalService: SucursalService,
    private toastr: ToastrService
  ) { }

  ngOnInit(): void {
    this.getSucursal();
  }

  getSucursal() {
    const sucursal = JSON.parse(localStorage.getItem('sucursal'));
    if (sucursal === null) {
      this.getAllSucursal();
    } else {
      var modal = document.getElementById("modal-sucursal");
      modal.style.display = "none";
    }
  }

  getAllSucursal() {
    this.sucursalService.getSucursal().subscribe(
      (res) => {
        if (res.status) {
          this.sucursal = res.data;
          var modal = document.getElementById("modal-sucursal");
          modal.style.display = "flex";
        }
      }
    )
  }

  addSucursalStorage() {
    if (this.sucursalId === 0) {
      alert('Seleccione una sucursal');
    } else {
      const json = {
        sucursal: this.sucursalId
      };
      localStorage.setItem('sucursal', JSON.stringify(json));
      var modal = document.getElementById("modal-sucursal");
      modal.style.display = "none";
      window.location.reload();
    }
  }

  selectSucursalCard(id) {
    console.log(id);
    for (let i = 0; i < this.sucursal.length; i++) {
      var card = document.getElementById('card_select_' + this.sucursal[i].id);
      card.style.backgroundColor = "deepskyblue";
    }
    var card = document.getElementById('card_select_' + id);
    card.style.backgroundColor = "#1037FF";
    this.sucursalId = id;
  }

}
