import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class SettingService {

    constructor(
        private http: HttpClient
    ) { }

    getSettings(){
        return this.http.get(`${environment.endpoint}/settings/getAll`).pipe(
            map((data: any) => {
                return data;
            })
        );
    }

    getImages() {
        return this.http.get(`${environment.endpoint}/settings/image/getAllNotNull`).pipe(
            map((data: any) => {
                return data;
            })
        );
    }

}
