import { Component, OnInit } from '@angular/core';
import { OrdersService } from 'src/app/services/orders.service';
import { DetailOrder } from 'src/app/models/order.models';
import { Router, ActivatedRoute } from '@angular/router';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-blog-full',
  templateUrl: './blog-full.component.html',
  styleUrls: ['./blog-full.component.css']
})
export class BlogFullComponent implements OnInit {

  public user = { userId : 0 };
  public order = [];
  public loadingOrder = false;
  public detail = new DetailOrder('','','','','','','','','','',[],'','','','','','','','','','','','','','',[],'','','','','','','','','','','','', '', '', '');
  public orderDetail = [];
  public loadingDetail = false;
  public image = { order : '' }

  constructor(
    private orderService : OrdersService,
    private router: Router,
    private settingService : SettingService
  ) { }

  ngOnInit(): void {
    const orderStorage = JSON.parse(localStorage.getItem('bannerOrder'));
    if (orderStorage) {
      this.image.order = orderStorage;
    } else {
      this.image.order = `"assets/img/prods/1920x500/wafflemediano.png"`;
    }
    document.getElementById("banner").style.backgroundImage = `url(${this.image.order})`;
    this.getSettingsImage();
    const users = JSON.parse(localStorage.getItem('user'));
    if (users) {
      this.user.userId = users.id;
      this.getOrder();
    }

    const setInterval = JSON.parse(localStorage.getItem('setIntervals'));
    clearInterval(setInterval);
    setTimeout(() => {
      localStorage.removeItem('setIntervals')
    }, 200);
  }

  getSettingsImage() {
    this.settingService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const order = resp.find(e => e.id == 6);
        if (order) {
          const orderURI = `${environment.store}/${order.upload}`;
          localStorage.setItem('bannerOrder', JSON.stringify(orderURI));
          this.image.order = orderURI;
        } else {
          localStorage.setItem('bannerOrder', `"assets/img/prods/1920x500/wafflemediano.png"`);
          this.image.order = `"assets/img/prods/1920x500/wafflemediano.png"`;
        }
        document.getElementById("banner").style.backgroundImage = `url(${this.image.order})`;
      }
    );
  }

  getOrder(){
    this.loadingOrder = true;
    this.orderService.getOrderUser(this.user).subscribe(
      (res) => {
        if (res.status) {
          this.order = res.data;
          setTimeout(() => {
            this.loadingOrder = false;
          }, 1000);
        }
      }
    );
  }

  getOrderDetail(){
    return new Promise((resolve, reject) => {
      this.orderService.getOrderUser(this.user).subscribe(
        (res) => {
          if (res.status) {
            this.order = res.data;
            console.log('Empieza primero');
            resolve(true);
          }
        }
      );
    });
  }

  async detailPedido(orderId){
    await this.getOrderDetail();
    this.loadingDetail = true;
    console.log('Termina aqui');
    this.orderDetail = this.order.filter((e) => e.id == orderId);
    console.log(this.orderDetail);
    this.detail = this.orderDetail[0];
    setTimeout(() => {
      this.loadingDetail = false;
    }, 500);
  }

  detailOrder(orderId) {
    this.loadingDetail = true;
    const order = this.order.filter((e) => e.id == orderId);
    localStorage.setItem('detailOrder', JSON.stringify(order));
    this.router.navigate(['/detalle-orden']);
  }

}
