import { Component, OnInit } from '@angular/core';
import { SucursalService } from 'src/app/services/sucursal.service';
import "leaflet/dist/leaflet.css";
import "leaflet-draw";
import "leaflet-draw/dist/leaflet.draw.css";
import * as GeoSearch from 'leaflet-geosearch';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

declare const L: any;
declare var google: any;

@Component({
  selector: 'app-locations',
  templateUrl: './locations.component.html',
  styleUrls: ['./locations.component.css']
})

export class LocationsComponent implements OnInit {

  sucursal = [];
  adress; map; lat; lng; latitud; longitude; drawnItems;
  public loading = false;
  public image = { stores : '' };
  public color = "";

  constructor(
    private sucursalService: SucursalService,
    private settingService : SettingService
  ) { }

  ngOnInit(): void {
    const storeStorage = JSON.parse(localStorage.getItem('bannerStores'));
    if (storeStorage) {
      this.image.stores = storeStorage;
    } else {
      this.image.stores = `"assets/img/especificos/crepeduo.png"`;
    }
    document.getElementById("banner").style.backgroundImage = `url(${this.image.stores})`;
    this.getSettingsImage();
  }

  ngAfterViewInit() {
    this.loading = false;
    this.getAllSucursal();

    const setInterval = JSON.parse(localStorage.getItem('setIntervals'));
    clearInterval(setInterval);
    setTimeout(() => {
      localStorage.removeItem('setIntervals')
    }, 200);
  }

  getSettingsImage() {
    this.settingService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const stores = resp.find(e => e.id == 4);
        if (stores) {
          const storesURI = `${environment.store}/${stores.upload}`;
          localStorage.setItem('bannerStores', JSON.stringify(storesURI));
          this.image.stores = storesURI;
        } else {
          localStorage.setItem('bannerStores', `"assets/img/especificos/crepeduo.png"`);
          this.image.stores = `"assets/img/especificos/crepeduo.png"`;
        }

        document.getElementById("banner").style.backgroundImage = `url(${this.image.stores})`;
      }
    );
  }

  markerMap(coordenates) {
    for(let i = 0; i < coordenates.length; i++) {
      let latitude = parseFloat(coordenates[i].latitude);
      let longitude = parseFloat(coordenates[i].longitude);
      let coordenatess = [latitude, longitude];
      setTimeout(() => {
        const mapContainer = document.getElementById('map_' + i);
        if (mapContainer) {
          var map = L.map('map_'+i).setView(coordenatess, 12);
          L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
            maxZoom: 15
          }).addTo(map);
          var customIcon = new L.Icon({
            iconUrl: 'https://i.pinimg.com/originals/25/62/aa/2562aacd1a4c2af60cce9629b1e05cf2.png',
            iconSize: [30, 30],
            iconAnchor: [15, 25]
          });
          L.marker(coordenatess, { icon: customIcon }).addTo(map);
        }
      }, 500);
    }
    setTimeout(() => {
      // this.loading = false;
      this.getColor();
    }, 1000);
  }

  getAllSucursal() {
    this.sucursalService.getSucursal().subscribe(
      (res) => {
        if (res.status) {
          this.sucursal = res.data;
          this.markerMap(this.sucursal);
        }
      }
    )
  }

  getColor () {
    this.settingService.getSettings().subscribe(
      (res) => {
        this.color = "#f39";
        this.loading = false;
      }
    );
  }

}
