import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SucursalService } from 'src/app/services/sucursal.service';
import { ToastrService } from 'ngx-toastr';
import { StoresOpen } from 'src/app/models/stores.models';
import Swal from 'sweetalert2';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {

  public getCarts = [];
  public val_cart;
  public totalPrincipal;
  suma = 0;
  public NewTotal = [];
  public igv;
  public subTotal: number;
  public igvDecimal;
  public menuTotal;
  public loading = false;
  public description: string;
  public covered = false;
  public minimoPrice = 0;
  public storesHours = new StoresOpen(0);
  public opennig = true;
  public image = { basket : '' }

  constructor(
    private router: Router,
    private sucursalService: SucursalService,
    private toastr: ToastrService,
    private settingService : SettingService
  ) {

    this.menuTotal = JSON.parse(localStorage.getItem('menuCheckout'));
  }

  ngOnInit(): void {
    const basketStorage = JSON.parse(localStorage.getItem('bannerBasket'));
    if (basketStorage) {
      this.image.basket = basketStorage;
    } else {
      this.image.basket = `"assets/img/prods/1920x500/ensaladafrutas.png"`;
    }
    document.getElementById("banner").style.backgroundImage = `url(${this.image.basket})`;
    this.getSettingsImage();
    const menuCheckout = JSON.parse(localStorage.getItem('menuCheckout'));
    this.getCart(menuCheckout);

    this.validateHours();

    const setInterval = JSON.parse(localStorage.getItem('setIntervals'));
    clearInterval(setInterval);
    setTimeout(() => {
      localStorage.removeItem('setIntervals')
    }, 200);

    this.getSucursal();
  }

  getSettingsImage() {
    this.settingService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const basket = resp.find(e => e.id == 5);
        if (basket) {
          const basketURI = `${environment.store}/${basket.upload}`;
          localStorage.setItem('bannerBasket', JSON.stringify(basketURI));
          this.image.basket = basketURI;
        } else {
          localStorage.setItem('bannerBasket', `"assets/img/prods/1920x500/ensaladafrutas.png"`);
          this.image.basket = `"assets/img/prods/1920x500/ensaladafrutas.png"`;
        }
        document.getElementById("banner").style.backgroundImage = `url(${this.image.basket})`;
      }
    );
  }

  getCart(menuCheckout) {
    this.getCarts = [];
    this.totalPrincipal = 0;
    this.suma = 0;
    this.NewTotal = [];
    this.igv = 0;
    this.igvDecimal = 0;
    this.subTotal = 0;
    if (menuCheckout) {
      this.getCarts = menuCheckout;
      for (let i = 0; i < this.getCarts.length; i++) {
        var arrryPrice = [];
        var suma = 0;
        if (this.getCarts[i].complement.length > 0) {
          var group = this.getCarts[i].complement;
          var quantityMenu = this.getCarts[i].quantity;
          var arrayComplementQuantity = [];
          for (let h = 0; h < quantityMenu; h++) {
            const jsonComplementQuantity = { 'complement': group };
            arrayComplementQuantity.push(jsonComplementQuantity);
          }
          this.getCarts[i].complementQuantity = arrayComplementQuantity;
          for (let j = 0; j < group.length; j++) {
            var complement = group[j].complement;
            for (let k = 0; k < complement.length; k++) {
              var price = complement[k].price;
              arrryPrice.push(parseFloat(price));
            }
          }
          arrryPrice.forEach(function (numero) {
            suma += numero;
          });
        }
        this.getCarts[i].totalComplement = suma;
        this.totalPrincipal = this.getCarts[i].menu.price * this.getCarts[i].quantity;
        this.suma += this.getCarts[i].menu.price * this.getCarts[i].quantity + (suma * this.getCarts[i].quantity);
        const subTotal = this.suma / 1.18;
        this.subTotal = Number((subTotal).toFixed(2));
        this.igv = this.suma - this.subTotal;
        this.igvDecimal = this.igv.toFixed(2);
        this.NewTotal.push(this.totalPrincipal);
        setTimeout(() => {
          if (this.getCarts[i].complement.length > 0) {
            document.getElementById('detailComplement_' + this.getCarts[i].menu.id + '_' + i).style.display = "none";
          }
        }, 100);
      }
    }
  }

  redirectCheckout() {
    let total = this.suma.toString();
    const encrypt = btoa(total);
    document.cookie = "_total=" + encrypt;
    this.loading = true;

    if (this.minimoPrice <= this.suma) {
      const sucursal = JSON.parse(localStorage.getItem('sucursal'));
      let sucursalId = 0;
      if (sucursal) {
        sucursalId = sucursal.sucursal;
      }

      if (this.covered) {
        localStorage.setItem('covered', JSON.stringify('Si'));
      } else {
        localStorage.setItem('covered', JSON.stringify('No'));
      }

      //localStorage.removeItem("menuCheckout");
      localStorage.removeItem("description");
      this.sucursalService.validatePrice({ storeId : sucursalId, price : total }).subscribe(
        (res) => {
          if (res.status) {
            setTimeout(() => {
              if (this.description) {
                localStorage.setItem("description", JSON.stringify(this.description));
              }
              localStorage.setItem("menuCheckout", JSON.stringify(this.getCarts));
              if (localStorage.getItem('token')) {
                this.router.navigate(['procesar-compra']);
              } else {
                sessionStorage.setItem('SESSION_ROUTE', JSON.stringify(true));
                this.router.navigate(['/registrarse']);
              }
            }, 1000)
          } else {
            this.loading = false;
            this.toastr.info('Mínimo de pedido es de s/ ' + res.result.minimum_order);
          }
        }
      );

    } else {
      this.loading = false;
      this.toastr.info('Mínimo de pedido es de s/ ' + this.minimoPrice);
    }
  }

  redirectCart() {
    this.router.navigate(['carta']);
  }

  getSucursal() {
    const sucursal = JSON.parse(localStorage.getItem('sucursal'));
    if (sucursal) {
      this.sucursalService.getSucursal().subscribe(
        (res) => {
          if (res.status) {
            if (sucursal) {
              const sucursalId = sucursal.sucursal;
              const data = res.data;
              const sucursalFilter = data.filter((e) => e.id == sucursalId);
              this.minimoPrice = sucursalFilter[0].minimum_order;
            }
          }
        }
      );
    } else {
      window.location.href = "#/inicio";
    }

  }

  deatilMenu(id, count) {
    if (document.getElementById('icon_plus_' + id + '_' + count).innerHTML === "-") {
      for (let i = 0; i < this.getCarts.length; i++) {
        if (this.getCarts[i].complement.length > 0) {
          document.getElementById('detailComplement_' + this.getCarts[i].menu.id + '_' + i).style.display = "none";
          document.getElementById('icon_plus_' + this.getCarts[i].menu.id + '_' + i).innerHTML = "+";
        }
      }
    } else {
      for (let i = 0; i < this.getCarts.length; i++) {
        if (this.getCarts[i].complement.length > 0) {
          document.getElementById('detailComplement_' + this.getCarts[i].menu.id + '_' + i).style.display = "none";
          document.getElementById('icon_plus_' + this.getCarts[i].menu.id + '_' + i).innerHTML = "+";
        }
      }
      if (document.getElementById('detailComplement_' + id + '_' + count)) {
        document.getElementById('icon_plus_' + id + '_' + count).innerHTML = "-";
        document.getElementById('detailComplement_' + id + '_' + count).style.display = "contents";
      }
    }
  }

  removeMenu(item) {
    localStorage.removeItem("menuCheckout");
    var i = this.getCarts.indexOf(item);
    if (i !== -1) {
      this.getCarts.splice(i, 1);
    }
    localStorage.setItem("menuCheckout", JSON.stringify(this.getCarts));
    this.getCart(this.getCarts);
  }

  less(id, i, row) {
    if (i > 1) {
      //localStorage.removeItem("menuCheckout");
      /*var index = this.getCarts.indexOf(row);
      if (index !== -1) {
        this.getCarts.splice(i, 1);
      }*/
      row.quantity = i - 1;
      //localStorage.setItem("menuCheckout", JSON.stringify(this.getCarts));
      this.getCart(this.getCarts);
    }
  }

  more(id, i, row) {
    var index = this.getCarts.filter((e) => e.id == id);
    index[0].quantity = i + 1;
    this.getCart(this.getCarts);
  }

  switch() {
    if (this.covered) {
      this.covered = false;
    }

    if (!this.covered) {
      this.covered = true;
    }
  }

  validateHours () {
    this.storesHours.storeId = JSON.parse(localStorage.getItem('sucursal')).sucursal;
    this.sucursalService.validateSucursal().subscribe(
      (res) => {
        const message = res.data[0].message;
        if (message == 'OPEN') {
          this.opennig = true;
        } else {
          this.opennig = false;
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }


}
