import { Component, OnInit } from '@angular/core';
import { FoodService } from 'src/app/services/food.service';
import { MenuBannerClass, MenuMoreOrderClass } from 'src/app/models/menu.models'
import { CategoryService } from 'src/app/services/category.service';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  public paginate = 0;
  public paginates = 0;
  public paginateOne = 0;
  public banner = [];
  public moreOrder = [];
  public bannerOne = [];
  public bannerGet = new MenuBannerClass(0, '', '', '', '', 0);
  public moreOrderGet = new MenuMoreOrderClass(0, '', '', '', '', 0);
  public validateSession = false;
  public loading = false;
  public repit = 0;
  public category = [];
  public settings = { text_coupon: '', url_coupon: '', file_coupon: '', text_big: '' };
  public image = { presentation: '', image_1: '', image_2: '', image_3: '', image_4: '', image_5: '', image_6: '', image_7: '', image_8: '', image_9: '', home: '' };
  public color = "";

  constructor(
    private foodService: FoodService,
    private categoryService: CategoryService,
    private settingService: SettingService
  ) { }

  ngOnInit(): void {
    const presentationStorage = JSON.parse(localStorage.getItem('presentation'));
    if (presentationStorage) {
      this.image.presentation = presentationStorage;
    } else {
      this.image.presentation = `"assets/img/nuestra-carta.png"`;
    }
    document.getElementById("banner").style.backgroundImage = `url(${this.image.presentation})`;

    const homeStorage = localStorage.getItem('home');
    if (homeStorage) {
      this.image.home = homeStorage;
    } else {
      this.image.home = "assets/img/especificos/nosotros.jpg";
    }
    this.getSettingsImage();
    this.getUser();
    this.getBanner();
    this.getMoreOrder();
    this.getOneBanner();
    this.avancedCarrousel();
    this.getCount();
    this.getSettings();
  }

  getImagesCarrousel() {
    const carrousel_1 = JSON.parse(localStorage.getItem('carrousel_1'));
    if (carrousel_1) {
      this.image.image_1 = carrousel_1;
    } else {
      this.image.image_1 = `assets/img/clients/1-min.png`;
    }

    const carrousel_2 = JSON.parse(localStorage.getItem('carrousel_2'));
    if (carrousel_2) {
      this.image.image_2 = carrousel_2;
    } else {
      this.image.image_2 = `assets/img/clients/2-min.png`;
    }

    const carrousel_3 = JSON.parse(localStorage.getItem('carrousel_3'));
    if (carrousel_3) {
      this.image.image_3 = carrousel_3;
    } else {
      this.image.image_3 = `assets/img/clients/3-min.png`;
    }

    const carrousel_4 = JSON.parse(localStorage.getItem('carrousel_4'));
    if (carrousel_4) {
      this.image.image_4 = carrousel_4;
    } else {
      this.image.image_4 = `assets/img/clients/4-min.png`;
    }

    const carrousel_5 = JSON.parse(localStorage.getItem('carrousel_5'));
    if (carrousel_5) {
      this.image.image_5 = carrousel_5;
    } else {
      this.image.image_5 = `assets/img/clients/5-min.png`;
    }

    const carrousel_6 = JSON.parse(localStorage.getItem('carrousel_6'));
    if (carrousel_6) {
      this.image.image_6 = carrousel_6;
    } else {
      this.image.image_6 = `assets/img/clients/6-min.png`;
    }

    const carrousel_7 = JSON.parse(localStorage.getItem('carrousel_7'));
    if (carrousel_7) {
      this.image.image_7 = carrousel_7;
    } else {
      this.image.image_7 = `assets/img/clients/7-min.png`;
    }

    const carrousel_8 = JSON.parse(localStorage.getItem('carrousel_8'));
    if (carrousel_8) {
      this.image.image_8 = carrousel_8;
    } else {
      this.image.image_8 = `assets/img/clients/8-min.png`;
    }

    const carrousel_9 = JSON.parse(localStorage.getItem('carrousel_9'));
    if (carrousel_9) {
      this.image.image_9 = carrousel_9;
    } else {
      this.image.image_9 = `assets/img/clients/9-min.png`;
    }
  }

  async getBanner() {
    this.loading = true;
    document.getElementById('animate-banner').className = "";
    const promise = new Promise((resolve, reject) => {
      this.foodService.getBanner(this.paginate).subscribe(
        (res) => {
          if (res.status) {
            resolve(res);
          }
        }
      );
    });
    const res: any = await promise.then(res => res).catch(err => err);

    this.banner = res.data;
    
    if (document.getElementById('animate-banner')) {
      document.getElementById('animate-banner').className = "animate-right";
    }

    var length = this.banner.length;
    if (length === 0) {
      this.paginate = 0;
      this.getBanner();
    }
    if (length > 0) {
      this.getByBanner(this.banner[0]);
    }

    console.log(this.loading, this.repit);
    this.getColor();
    
  }

  getUser() {
    const user = JSON.parse(localStorage.getItem('user'));
    if (user) {
      this.validateSession = true;
    } else {
      this.validateSession = false;
    }
  }

  getOneBanner() {
    this.foodService.getOneBanner(this.paginateOne).subscribe(
      (res) => {
        if (res.status) {
          this.bannerOne = res.data;
          var length = this.bannerOne.length;
          if (length === 0) {
            this.paginateOne = 0;
            this.getOneBanner();
          }
          if (length > 0) {
            this.getByBanner(this.bannerOne[0]);
          }
        }
      }
    );
  }

  getSettingsImage() {
    this.settingService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const presentation = resp.find(e => e.id == 1);
        if (presentation) {
          const presentationURI = `${environment.store}/${presentation.upload}`;
          localStorage.setItem('presentation', JSON.stringify(presentationURI));
          this.image.presentation = presentationURI;
        } else {
          localStorage.setItem('presentation', `"assets/img/nuestra-carta.png"`);
          this.image.presentation = `"assets/img/nuestra-carta.png"`;
        }
        document.getElementById("banner").style.backgroundImage = `url(${this.image.presentation})`;

        const home = resp.find(e => e.id == 19);
        if (home) {
          const homeURI = `${environment.store}/${home.upload}`;
          localStorage.setItem('home', homeURI);
          this.image.home = homeURI;
        } else {
          localStorage.setItem('home', "assets/img/especificos/nosotros.jpg");
          this.image.home = "assets/img/especificos/nosotros.jpg";
        }

        const image_1 = resp.find(e => e.id == 10);
        if (image_1) {
          const image_1URI = `${environment.store}/${image_1.upload}`;
          localStorage.setItem('carrousel_1', image_1URI);
          this.image.image_1 = image_1URI;
        } else {
          localStorage.setItem('carrousel_1', `assets/img/clients/1-min.png`);
          this.image.image_1 = `assets/img/clients/1-min.png`;
        }

        const image_2 = resp.find(e => e.id == 11);
        if (image_2) {
          const image_2URI = `${environment.store}/${image_2.upload}`;
          localStorage.setItem('carrousel_2', image_2URI);
          this.image.image_2 = image_2URI;
        } else {
          localStorage.setItem('carrousel_2', `assets/img/clients/2-min.png`);
          this.image.image_2 = `assets/img/clients/2-min.png`;
        }

        const image_3 = resp.find(e => e.id == 12);
        if (image_3) {
          const image_3URI = `${environment.store}/${image_3.upload}`;
          localStorage.setItem('carrousel_3', image_3URI);
          this.image.image_3 = image_3URI;
        } else {
          localStorage.setItem('carrousel_3', `assets/img/clients/3-min.png`);
          this.image.image_3 = `assets/img/clients/3-min.png`;
        }

        const image_4 = resp.find(e => e.id == 13);
        if (image_4) {
          const image_4URI = `${environment.store}/${image_4.upload}`;
          localStorage.setItem('carrousel_4', image_4URI);
          this.image.image_4 = image_4URI;
        } else {
          localStorage.setItem('carrousel_4', `assets/img/clients/4-min.png`);
          this.image.image_4 = `assets/img/clients/4-min.png`;
        }

        const image_5 = resp.find(e => e.id == 14);
        if (image_5) {
          const image_5URI = `${environment.store}/${image_5.upload}`;
          localStorage.setItem('carrousel_5', image_5URI);
          this.image.image_5 = image_5URI;
        } else {
          localStorage.setItem('carrousel_5', `assets/img/clients/5-min.png`);
          this.image.image_5 = `assets/img/clients/5-min.png`;
        }

        const image_6 = resp.find(e => e.id == 15);
        if (image_6) {
          const image_6URI = `${environment.store}/${image_6.upload}`;
          localStorage.setItem('carrousel_6', image_6URI);
          this.image.image_6 = image_6URI;
        } else {
          localStorage.setItem('carrousel_6', `assets/img/clients/6-min.png`);
          this.image.image_6 = `assets/img/clients/6-min.png`;
        }

        const image_7 = resp.find(e => e.id == 16);
        if (image_7) {
          const image_7URI = `${environment.store}/${image_7.upload}`;
          localStorage.setItem('carrousel_7', image_7URI);
          this.image.image_7 = image_7URI;
        } else {
          localStorage.setItem('carrousel_7', `assets/img/clients/7-min.png`);
          this.image.image_7 = `assets/img/clients/7-min.png`;
        }

        const image_8 = resp.find(e => e.id == 17);
        if (image_8) {
          const image_8URI = `${environment.store}/${image_8.upload}`;
          localStorage.setItem('carrousel_8', image_8URI);
          this.image.image_8 = image_8URI;
        } else {
          localStorage.setItem('carrousel_8', `assets/img/clients/8-min.png`);
          this.image.image_8 = `assets/img/clients/8-min.png`;
        }

        const image_9 = resp.find(e => e.id == 18);
        if (image_9) {
          const image_9URI = `${environment.store}/${image_9.upload}`;
          localStorage.setItem('carrousel_9', image_9URI);
          this.image.image_9 = image_9URI;
        } else {
          localStorage.setItem('carrousel_9', `assets/img/clients/9-min.png`);
          this.image.image_9 = `assets/img/clients/9-min.png`;
        }

      }
    );
  }

  lessOne() {
    if (this.paginateOne > -1) {
      this.paginateOne = this.paginateOne - 1;
      this.getOneBanner();
    }
  }

  increaseOne() {
    this.paginateOne = this.paginateOne + 1;
    this.getOneBanner();
  }

  less() {
    if (this.paginate > -1) {
      this.paginate = this.paginate - 1;
      this.getBanner();
    }
  }

  getSettings() {
    this.settingService.getSettings().subscribe(
      (res) => {
        const data = res.data;
        this.settings.text_coupon = data.text_coupon.value;
        this.settings.url_coupon = data.url_coupon.value;
        this.settings.text_big = data.text_big.value;
        this.settings.file_coupon = 'https://iconicoperu.com/iconicobackend/storage/app/public/' + data.file_coupon.value;
      }
    );
  }

  increase() {
    this.paginate = this.paginate + 1;
    this.getBanner();
  }

  getByBanner(banner = null) {
    this.bannerGet.id = banner.id;
    this.bannerGet.title = banner.title;
    this.bannerGet.detail = banner.detail;
    this.bannerGet.specification = banner.specification;
    this.bannerGet.image_url = banner.image_url;
    this.bannerGet.price = banner.price;
  }

  getMoreOrder() {
    this.foodService.getMoreOrder(this.paginates).subscribe(
      (res) => {
        if (res.status) {
          this.moreOrder = res.data;
          var length = this.moreOrder.length;
          if (length === 0) {
            this.paginates = 0;
            this.getMoreOrder();
          }
        }
      }
    );
  }

  lessMoreOrder() {
    if (this.paginates > -1) {
      this.paginates = this.paginates - 1;
      this.getMoreOrder();
    }
  }

  increaseMoreOrder() {
    this.paginates = this.paginates + 1;
    this.getMoreOrder();
  }

  avancedCarrousel() {
    var setIntervals = setInterval(() => {
      //@ts-ignore
      if (document.getElementById("bannerslice")) {
        //@ts-ignore
        document.getElementById("bannerslice").click();
      }
    }, 7000);

    localStorage.setItem('setIntervals', JSON.stringify(setIntervals));
  }

  carta() {
    document.getElementById('home').style.color = "white";
    document.getElementById('carta').style.color = "white";
    document.getElementById('sucursal').style.color = "white";
    document.getElementById('cart').style.color = "white";
    setTimeout(() => {
      document.getElementById('user').style.color = "white";
      if (this.validateSession) {
        document.getElementById('order').style.color = "white";
      }
    }, 200);
    window.location.href = "#/carta";
  }

  getCount() {
    this.categoryService.getCountCategory().subscribe(
      (res) => {
        this.category = res.data;
      }
    );
  }

  getColor () {
    if (this.loading = true && this.repit === 0) {
      this.settingService.getSettings().subscribe(
        (res) => {
          this.color = "#f39";
          this.loading = false;
          this.repit = this.repit + 1;
        }
      );
    }
  }

}
