import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ComplementService {

  constructor(
    private http: HttpClient
  ) { }

  listGroups() {
    return this.http.get(`${environment.endpoint}/complements/group/getAll`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  listComplement(data) {
    return this.http.get(`${environment.endpoint}/complements/get?groupId=` + data,).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getGroupComplement(menuId) {
    return this.http.get(`${environment.endpoint}/complements/group/getComplement?menuId=${menuId}`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getHeaders() {
    var getToken = JSON.parse(localStorage.getItem('token'));
    var token = "Bearer " + getToken;
    const headers = new HttpHeaders({ 'Authorization': token });
    return headers;
  }

}
