import { Component, OnInit } from '@angular/core';
import { icon, latLng, marker, polyline, tileLayer } from 'leaflet';
import { UserClass, UserProfileClass, PasswordClass } from 'src/app/models/user.models';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  // Define our base layers so we can reference them multiple times
  streetMaps = tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/Canvas/World_Light_Gray_Base/MapServer/tile/{z}/{y}/{x}', {
    attribution: 'Tiles &copy; Esri &mdash; Esri, DeLorme, NAVTEQ',
    maxZoom: 16
  });
  wMaps = tileLayer('http://maps.wikimedia.org/osm-intl/{z}/{x}/{y}.png', {
    detectRetina: true,
    attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
  });

  // Marker for the top of Mt. Ranier
  summit = marker([51.5, -0.09], {
    icon: icon({
      iconSize: [32, 32],
      iconAnchor: [13, 41],
      iconUrl: 'assets/img/misc/marker.png',
      shadowUrl: 'leaflet/marker-shadow.png'
    })
  });

  // Path from paradise to summit - most points omitted from this example for brevity
  route = polyline([[46.78465227596462, -121.74141269177198],
  [46.80047278292477, -121.73470708541572],
  [46.815471360459924, -121.72521826811135],
  [46.8360239546746, -121.7323131300509],
  [46.844306448474526, -121.73327445052564],
  [46.84979408048093, -121.74325201660395],
  [46.853193528950214, -121.74823296256363],
  [46.85322881676257, -121.74843915738165],
  [46.85119913890958, -121.7519719619304],
  [46.85103829018772, -121.7542376741767],
  [46.85101557523012, -121.75431755371392],
  [46.85140013694763, -121.75727385096252],
  [46.8525277543813, -121.75995212048292],
  [46.85290292836726, -121.76049157977104],
  [46.8528160918504, -121.76042997278273]]);

  // Layers control object with our two base layers and the three overlay layers
  layersControl = {
    baseLayers: {
      'Street Maps': this.streetMaps,
      'Wikimedia Maps': this.wMaps
    },
    overlays: {
      'Mt. Rainier Summit': this.summit,
      'Mt. Rainier Climb Route': this.route
    }
  };


  // Set the initial set of displayed layers (we could also use the leafletLayers input binding for this)
  options = {
    layers: [this.streetMaps, this.route, this.summit],
    zoom: 16,
    scrollWheelZoom: false,
    center: latLng([51.5, -0.09])
  };



  public user = new UserClass('', '', '', '', '', '', '', '', '', '', '', '', '', '', 0, 0, [], 'GOOGLE', '', 0);
  public userProfile = new UserProfileClass(0,'','');
  public passwordProfile = new PasswordClass(0, '', '');
  public password : string;
  public newPassword : string;

  constructor(
    private userService: UserService,
    private router: Router,
    private toastr: ToastrService
  ) { }

  ngOnInit(): void {
    this.getUsers();
  }


  getUsers() {
    const user = JSON.parse(localStorage.getItem('user'));
    if (user) {
      //this.user = user;
      this.userService.getUser(user.id).subscribe(
        (res) => {
          this.user = res.data[0];
        }
      );
    }
  }

  logout() {
    localStorage.removeItem('user');
    localStorage.removeItem('token');
    this.router.navigate(['/login'])
      .then(() => {
        window.location.reload();
      });
  }

  updateProfile(){
    this.userProfile.profileId = parseInt(this.user.id);
    this.userProfile.name = this.user.name;
    this.userProfile.mobile_number = this.user.mobile_number;
    console.log(this.user);
    if (this.user.mobile_number) {
      const numberoMobile = this.user.mobile_number.toString();
      if (numberoMobile.length === 9) {
        this.userService.updateProfile(this.userProfile).subscribe(
          (res) => {
            if (res.status) {
              this.toastr.success(res.message);
    
              const user = JSON.parse(localStorage.getItem('user'));
    
              if (user) {
                user.mobile_number = this.user.mobile_number;
                localStorage.setItem('user', JSON.stringify(user));
              }
              
              if ( this.password ) {
                this.passwordProfile.profileId = parseInt(this.user.id);
                this.passwordProfile.password = this.password;
                this.passwordProfile.newPassword = this.newPassword;
                this.userService.updatePassword(this.passwordProfile).subscribe(
                  (data) => {
                    if (data.status) {
                      this.toastr.success(data.message);
                    } else {
                      this.toastr.warning("Error al actualizar la constraseña");
                    }
                  },
                  (err) => {
                    this.toastr.error("Error en el servidor");
                  }
                );
              }
            }
          },
          (err) => {
            this.toastr.info('Al parecer el teléfono está en uso');
          }
        );
      } else {
        this.toastr.info('Por favor ingrese un número de celular de 9 dígitos');
      }
    } else {
      this.toastr.info('Por favor ingrese un número de celular');
    }
  }

  maxNumbersPhone(e) {
    var phone = e.target['value'];
    if (phone.length > 8) {
      return false;

    } else {
      return '';
    }
  }

}
