import { Component, OnInit } from '@angular/core';
import { CategoryService } from 'src/app/services/category.service';
import { FoodService } from 'src/app/services/food.service';
import { Router, ActivatedRoute } from '@angular/router';
import { SucursalService } from 'src/app/services/sucursal.service';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { StoresOpen } from 'src/app/models/stores.models';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-menu-one',
  templateUrl: './menu-one.component.html',
  styleUrls: ['./menu-one.component.css']
})
export class MenuOneComponent implements OnInit {

  public storesHours = new StoresOpen(0);
  allCategory = [];
  allFood = [];
  categoryId = 0;
  public loading = false;
  public loadingBody = false;
  public opennig = true;
  public image = { bannerCart : '' }
  public color = "";

  customOptions: OwlOptions = {
    loop: true,
    mouseDrag: false,
    touchDrag: false,
    pullDrag: false,
    dots: false,
    navSpeed: 700,
    navText: ['', ''],
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 2
      },
      740: {
        items: 3
      },
      940: {
        items: 6
      }
    },
    nav: true
  }

  constructor(
    private categoryService : CategoryService,
    private foodService : FoodService,
    private sucursalService : SucursalService,
    private router : Router,
    private settingService : SettingService
  ) { }

  ngOnInit(): void {
    this.loadingBody = true;
    const bannerCartStorage = localStorage.getItem('bannerCart');
    if (bannerCartStorage) {
      this.image.bannerCart = bannerCartStorage;
    } else {
      this.image.bannerCart = `assets/img/heladobanner.png`;
    }
    document.getElementById("bannerCart").style.backgroundImage = `url(${this.image.bannerCart})`;
    this.getColor();
    this.getSucursal();
    this.getSettingsImage();
    const setInterval = JSON.parse(localStorage.getItem('setIntervals'));
    clearInterval(setInterval);
    setTimeout(() => {
      localStorage.removeItem('setIntervals')
    }, 200);
  }

  getSucursal() {
    const sucursal = JSON.parse(localStorage.getItem('sucursal'));
    this.validateHours(sucursal);
    this.getCategory();
  }

  getSettingsImage() {
    this.settingService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const bannerCart = resp.find(e => e.id == 2);
        if (bannerCart) {
          const bannerCartURI = `${environment.store}/${bannerCart.upload}`;
          localStorage.setItem('bannerCart', bannerCartURI);
          this.image.bannerCart = bannerCartURI;
        } else {
          localStorage.setItem('bannerCart', `assets/img/heladobanner.png`);
          this.image.bannerCart = `assets/img/heladobanner.png`;
        }

        document.getElementById("bannerCart").style.backgroundImage = `url(${this.image.bannerCart})`;
      }
    );
  }

  validateHours (sucursal) {
    this.storesHours.storeId = sucursal.sucursal;
    this.sucursalService.validateSucursal().subscribe(
      (res) => {
        const message = res.data[0].message;
        if (message == 'OPEN') {
          this.opennig = true;
        } else {
          this.opennig = false;
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }

  getCategory() {
    this.categoryService.getCategory().subscribe(
      (res) => {
        this.allCategory = res.data;
        this.categoryId = res.data[0].id;
        setTimeout(() => {
          this.loadingBody = false;
        }, 600);
        this.getFood(this.categoryId);
      }
    )
  }

  getFood(categoryId, loading = false) {
    // this.loading = false;
    this.foodService.getFood(categoryId).subscribe(
      (data) => {
        this.allFood = data.data;
        // this.loading = true;
        if (loading) {
          setTimeout(() => {
            window.scrollTo(0, 500);
          }, 300);
        }
      }
    )
  }

  menuRedirect(id) {
    this.router.navigate(['carta/' + id]);
  }

  getColor () {
    this.loading = false;
    this.settingService.getSettings().subscribe(
      (res) => {
        this.color = "#f39";
        console.log(this.color);
        this.loading = true;
      }
    );
  }

}
