import { Component, OnInit } from '@angular/core';
import { SocialAuthService } from "angularx-social-login";
import { FacebookLoginProvider, GoogleLoginProvider } from "angularx-social-login";
import { SocialUser } from "angularx-social-login";
import { AuthenticationClass } from 'src/app/models/user.models';
import { UserService } from 'src/app/services/user.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  user: SocialUser;
  loggedIn: boolean;
  public users = { email : '' };

  userAuth = new AuthenticationClass('','','');

  public loading = false;
  public logo;

  public validateEmail = false;

  constructor(
    private authService: SocialAuthService,
    private userService: UserService,
    private toastr: ToastrService,
    private router: Router,
    private settingsService : SettingService
  ) { }


  ngOnInit(): void {
    const logoStorage = localStorage.getItem('logoIcon');
    if (logoStorage) {
      this.logo = logoStorage;
    } else {
      this.logo = `assets/img/logoheader.png`;
    }
    document.getElementById("logoLogin").style.backgroundImage = `url(${this.logo})`;
    // document.getElementById("logoLogin").style.width = "100%";
    // document.getElementById("logoLogin").style.height = "100%";
    document.getElementById("logoLogin").style.backgroundRepeat = "no-repeat";
    this.getSettings();
    this.authService.authState.subscribe((user) => {
      var url = this.router.url;
      const split = url.split('/');
      if (split[1] === "iniciar-sesion") { 
        this.user = user;
        this.users.email = user.email;
        this.loginSocial(this.users);
      }
    });
    
    const setInterval = JSON.parse(localStorage.getItem('setIntervals'));
    clearInterval(setInterval);
    setTimeout(() => {
      localStorage.removeItem('setIntervals')
    }, 200);
  }

  signInWithGoogle(): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID);
  }

  signInWithFB(): void {
    this.authService.signIn(FacebookLoginProvider.PROVIDER_ID);
  }

  signOut(): void {
    this.authService.signOut();
  }

  loginSocial(body) {
    this.loading = true;
    this.userService.loginSocial(body).subscribe(
      (data) => {
        if(data.token) {
          var token = data.token;
          var user = data.user;
          localStorage.setItem('token', JSON.stringify(token));
          localStorage.setItem('user', JSON.stringify(user));
          this.toastr.success('Logueo satisfactoriamente');
          setTimeout(() => {
            window.location.href = "#/mi-perfil";
          }, 1000);
        } else {
          setTimeout(() => {
            this.loading = false;
            this.toastr.warning('Al parecer no se encuentra registrado.');
          }, 800);
        }
      },
      (error) => {
        setTimeout(() => {
          this.loading = false;
          this.toastr.warning('Credenciales incorrectas');
        }, 800);
      }
    )
  }

  login() {
    this.loading = true;
    this.userAuth.role = "customer";
    this.userService.login(this.userAuth).subscribe(
      (data) => {
        if(data.token) {
          var token = data.token;
          var user = data.user;
          localStorage.setItem('token', JSON.stringify(token));
          localStorage.setItem('user', JSON.stringify(user));
          this.toastr.success('Logueo satisfactoriamente');
          if (JSON.parse(sessionStorage.getItem('SESSION_ROUTE'))) {
            sessionStorage.removeItem('SESSION_ROUTE');
            setTimeout(() => {
              window.location.href = "#/procesar-compra";
            }, 1000);
          } else {
            setTimeout(() => {
              window.location.href = "#/inicio";
            }, 1000);
          }
        } else {
          setTimeout(() => {
            this.loading = false;
            this.toastr.info('Credenciales incorrectas');
          }, 800);
        }
      },
      (error) => {
        /*setTimeout(() => {
          this.loading = false;
          this.toastr.warning('Credenciales incorrectas');
        }, 800);*/
        this.userAuth.role = "waiter";
        this.userService.login(this.userAuth).subscribe(
          (res) => {
            if(res.token) {
              var token = res.token;
              var user = res.user;
              localStorage.setItem('token', JSON.stringify(token));
              localStorage.setItem('user', JSON.stringify(user));
              this.toastr.success('Logueo satisfactoriamente');
              setTimeout(() => {
                window.location.href = "#/inicio";
              }, 1000);
            } else {
              setTimeout(() => {
                this.loading = false;
                this.toastr.info('Credenciales incorrectas');
              }, 800);
            }
          },
          (err) => {
            setTimeout(() => {
              this.loading = false;
              this.toastr.warning('Credenciales incorrectas');
            }, 800);
          }
        );
      }
    )
  }

  register(){
    this.router.navigate(['/registrarse']);
  }

  getSettings() {
    this.settingsService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const logo = resp.find(e => e.id == 20);
        if (logo) {
          const logoURI = `${environment.store}/${logo.upload}`;
          localStorage.setItem('logoIcon', logoURI);
          this.logo = logoURI;
        } else {
          localStorage.setItem('logoIcon', `assets/img/logoheader.png`);
          this.logo = `assets/img/logoheader.png`;
        }
        document.getElementById("logoLogin").style.backgroundImage = `url(${this.logo})`;
        // document.getElementById("logoLogin").style.width = "100%";
        // document.getElementById("logoLogin").style.height = "100%";
        document.getElementById("logoLogin").style.backgroundRepeat = "no-repeat";
      }
    );
  }

}
