import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CategoryService {

  constructor(
    private http: HttpClient
  ) { }


  getCategory() {
    return this.http.get(`${environment.endpoint}/category/getAll`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getCountCategory() {
    return this.http.get(`${environment.endpoint}/category/getCount`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

}
