import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class OrdersService {

    constructor(
        private http: HttpClient
    ) { }

    createOrder(body) {
        const headers = this.getHeaders();
        return this.http.post(`${environment.endpoint}/orders/create`, body, { headers }).pipe(
            map((data: any) => {
                return data;
            })
        );
    }

    uploadsFile(orderId : string, file : File) {
        const headers = this.getHeaders();
        const fd = new FormData();
        fd.append('orderId', orderId);
        fd.append('image', file);
        return this.http.post(`${environment.endpoint}/orders/uploads`, fd, {headers});
    }

    getOrderUser(body){
        const headers = this.getHeaders();
        return this.http.post(`${environment.endpoint}/orders/get/all`, body, { headers }).pipe(
            map((data: any) => {
                return data;
            })
        );
    }

    getHeaders() {
        var getToken = JSON.parse(localStorage.getItem('token'));
        var token = "Bearer " + getToken;
        const headers = new HttpHeaders({ 'Authorization': token });
        return headers;
    }

}
