import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class FoodService {

  constructor(
    private http: HttpClient
  ) { }

  getFood(categoryId) {
    let sucursalId = this.getSucursal();
    return this.http.get(`${environment.endpoint}/plate/getAll?categoryId=${categoryId}&sucursalId=${sucursalId}`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getMenuBy(menuId) {
    return this.http.get(`${environment.endpoint}/plate/get?menuId=${menuId}`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getBanner(paginate){
    return this.http.get(`${environment.endpoint}/admin/plate/getBanner?paginate=${paginate}`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getOneBanner(paginate){
    return this.http.get(`${environment.endpoint}/admin/plate/getOneBanner?paginate=${paginate}`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getMoreOrder(paginate){
    return this.http.get(`${environment.endpoint}/admin/plate/getMoreOrder?paginate=${paginate}`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getSucursal(){
    const sucursal = JSON.parse(localStorage.getItem('sucursal'));
    if (sucursal != null) {
      return sucursal.sucursal;
    }
  }

}
