import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class SucursalService {

  constructor(
    private http: HttpClient
  ) { }


  getSucursal() {
    return this.http.get(`${environment.endpoint}/sucursal/getAll`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getCoordenates() {
    let sucursalId = this.getSucursalStorage();
    return this.http.get(`${environment.endpoint}/sucursal/coordenates/get?storeId=${sucursalId}`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  validateSucursal() {
    let sucursalId = this.getSucursalStorage();
    return this.http.get(`${environment.endpoint}/sucursal/validate/openning?storeId=${sucursalId}`).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  getSucursalStorage(){
    const sucursal = JSON.parse(localStorage.getItem('sucursal'));
    if (sucursal != null) {
      return sucursal.sucursal;
    }
  }

  validateHourStore(body) {
    let sucursalId = this.getSucursalStorage();
    return this.http.post(`${environment.endpoint}/sucursal/hour/open`, body).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

  validatePrice(body) {
    return this.http.post(`${environment.endpoint}/sucursal/validatePriceStore`, body).pipe(
      map((data: any) => {
        return data;
      })
    );
  }

}
