import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { SendEmail } from 'src/app/models/user.models';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-recover-password',
  templateUrl: './recover-password.component.html',
  styleUrls: ['./recover-password.component.css']
})
export class RecoverPasswordComponent implements OnInit {

  public email : '';
  public user = new SendEmail('', '');
  public response = false;
  public code : String;
  public loading = false;

  constructor(
    private toastr: ToastrService,
    private userService : UserService
  ) { }

  ngOnInit(): void {
  }

  sendEmail() {
    if ( this.email === "" || this.email === undefined ) {
      this.toastr.info('Por favor ingrese su correo');
    } else {
      this.user.email = this.email;
      this.user.subValidate = this.token() + '_' + btoa(this.email);
      this.loading = true;
      this.userService.recoverPassword(this.user).subscribe(
        (res) => {
          setTimeout(() => {
            if (res.status) {
              this.response = true;
              this.code = "200";
              this.loading = false;
            } else {
              this.response = true;
              this.code = "400";
              this.loading = false;
            }
          }, 1000);
        }
      );
    }
  }

  random() {
    return Math.random().toString(36).substr(2); // Eliminar `0.`
  };
 
  token() {
    let randomVar = this.random();
    for (let i = 0; i < 10; i++) {
        randomVar += this.random()
    }
    return randomVar;
  };

}
