import { Component, OnInit } from '@angular/core';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-footer-shared',
  templateUrl: './footer-shared.component.html',
  styleUrls: ['./footer-shared.component.css']
})
export class FooterSharedComponent implements OnInit {

  public logo;

  constructor(
    private settingsService : SettingService
  ) { }

  ngOnInit(): void {
    const logoStorage = localStorage.getItem('logoFooter');
    if (logoStorage) {
      this.logo = logoStorage;
    } else {
      this.logo = `assets/img/logoheader.png`;
    }
    this.getSettings();
  }

  getSettings() {
    this.settingsService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const logo = resp.find(e => e.id == 8);
        if (logo) {
          const logoURI = `${environment.store}/${logo.upload}`;
          localStorage.setItem('logoFooter', logoURI);
          this.logo = logoURI;
        } else {
          localStorage.setItem('logoFooter', `assets/img/logoheader.png`);
          this.logo = `assets/img/logoheader.png`;
        }
      }
    );
  }

}
