import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SucursalService } from 'src/app/services/sucursal.service';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-navbar-shared',
  templateUrl: './navbar-shared.component.html',
  styleUrls: ['./navbar-shared.component.css']
})
export class NavbarSharedComponent implements OnInit {
  user;
  public getCarts = [];
  public val_cart;
  public countCesta;
  public validateSession = false;
  public sucursalArray = [];
  public sucursalIdData = 0;
  public open = "";
  public close = "";
  public phoneBoolean = false;
  public logo : String;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private sucursalService : SucursalService,
    private settingsService : SettingService
  ) { }

  ngOnInit(): void {
    const logoStorage = localStorage.getItem('logoIcon');
    if (logoStorage) {
      this.logo = logoStorage;
    } else {
      this.logo = `assets/img/logoheader.png`;
    }
    this.getSettings();
    this.sucursalOpen();
    setInterval(() => {
      this.getUser();
      this.getCart();
      var url = this.router.url;
      const split = url.split('/');
      if (split[1] === "inicio") { this.home(); }
      if (split[1] === "carta") { this.carta(); }
      if (split[1] === "sucursal") { this.sucursal(); }
      if (split[1] === "cesta") { this.cesta(); }
      if (split[1] === "iniciar-sesion") { this.iniciar_sesion(); }
      if (split[1] === "perfil") { this.profile(); }
      if (split[1] === "procesar-compra") { this.processStore(); }
      if (split[1] === "mis-ordenes") { this.order(); }
      if (split[1] === "detalle-orden") { this.resumenPedido(); }
      if (split[1] === "registrarse") { this.profile(); }

      const user = JSON.parse(localStorage.getItem('user'));
      if (user) {
        const mobile = user.mobile_number;
        if (mobile == "" || mobile == null || mobile == undefined) {
          this.phoneBoolean = true;
        } else {
          this.phoneBoolean = false;
        }
      }
    }, 500);
  }

  sucursalOpen(){
    const sucursal = JSON.parse(localStorage.getItem('sucursal'));
    if (sucursal) {
      const json = { storeId : sucursal.sucursal }
      this.sucursalService.validateHourStore(json).subscribe(
        (res) => {
          console.log(res);
          this.open = res.data.open;
          this.close = res.data.close;
        }
      );
    }
  }

  getSettings() {
    this.settingsService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const logo = resp.find(e => e.id == 8);
        if (logo) {
          const logoURI = `${environment.store}/${logo.upload}`;
          localStorage.setItem('logoIcon', logoURI);
          this.logo = logoURI;
        } else {
          localStorage.setItem('logoIcon', `assets/img/logoheader.png`);
          this.logo = `assets/img/logoheader.png`;
        }
      }
    );
  }

  redirectCesta() {
    this.cesta();
    this.router.navigate(['/cesta']);
  }

  redirectLogin(){
    this.profile();
    this.router.navigate(['/iniciar-sesion']);
  }

  redirectProfile(){
    this.profile();
    this.router.navigate(['/perfil']);
  }

  redirectOrder(){
    this.order();
    this.router.navigate(['/mis-ordenes']);
  }

  getCart() {
    const menuCheckout = JSON.parse(localStorage.getItem('menuCheckout'));
    if (menuCheckout) {
      this.getCarts = menuCheckout;
      this.countCesta = this.getCarts.length;
    } else {
      this.countCesta = 0;
    }
  }

  getUser(){
    this.user = JSON.parse(localStorage.getItem('user'));
    if (this.user) {
      this.validateSession = true;
    } else {
      this.validateSession = false;
    }
  }

  home(){
    document.getElementById('home').style.color = "#1D2228";
    document.getElementById('carta').style.color = "#1D2228";
    document.getElementById('sucursal').style.color = "#1D2228";
    document.getElementById('cart').style.color = "#1D2228";
    setTimeout(() => {
      document.getElementById('user').style.color = "#1D2228";
      if (this.validateSession) {
        document.getElementById('order').style.color = "#1D2228";
      }
    }, 200);
  }

  carta(){
    document.getElementById('home').style.color = "white";
    document.getElementById('carta').style.color = "white";
    document.getElementById('sucursal').style.color = "white";
    document.getElementById('cart').style.color = "white";
    setTimeout(() => {
      document.getElementById('user').style.color = "white";
      if (this.validateSession) {
        document.getElementById('order').style.color = "white";
      }
    }, 200)
  }

  sucursal(){
    document.getElementById('home').style.color = "white";
    document.getElementById('carta').style.color = "white";
    document.getElementById('sucursal').style.color = "white";
    document.getElementById('cart').style.color = "white";
    setTimeout(() => {
      document.getElementById('user').style.color = "white";
      if (this.validateSession) {
        document.getElementById('order').style.color = "white";
      }
    }, 150);
  }

  cesta(){
    document.getElementById('home').style.color = "white";
    document.getElementById('carta').style.color = "white";
    document.getElementById('sucursal').style.color = "white";
    document.getElementById('cart').style.color = "white";
    setTimeout(() => {
      document.getElementById('user').style.color = "white";
      if (this.validateSession) {
        document.getElementById('order').style.color = "white";
      }
    }, 200);
  }

  iniciar_sesion(){
    document.getElementById('home').style.color = "#1D2228";
    document.getElementById('carta').style.color = "#1D2228";
    document.getElementById('sucursal').style.color = "#1D2228";
    document.getElementById('cart').style.color = "#1D2228";
    setTimeout(() => {
      document.getElementById('user').style.color = "#1D2228";
    }, 200);
  }

  profile(){
    document.getElementById('home').style.color = "#1D2228";
    document.getElementById('carta').style.color = "#1D2228";
    document.getElementById('sucursal').style.color = "#1D2228";
    document.getElementById('cart').style.color = "#1D2228";
    setTimeout(() => {
      document.getElementById('user').style.color = "#1D2228";
      if (this.validateSession) {
        document.getElementById('order').style.color = "#1D2228";
      }
    }, 200);
  }

  order(){
    document.getElementById('home').style.color = "white";
    document.getElementById('carta').style.color = "white";
    document.getElementById('sucursal').style.color = "white";
    document.getElementById('cart').style.color = "white";
    setTimeout(() => {
      document.getElementById('user').style.color = "white";
      document.getElementById('order').style.color = "white";
    }, 200);
  }

  processStore(){
    document.getElementById('home').style.color = "white";
    document.getElementById('carta').style.color = "white";
    document.getElementById('sucursal').style.color = "white";
    document.getElementById('cart').style.color = "white";
    setTimeout(() => {
      document.getElementById('user').style.color = "white";
      if (document.getElementById('order')) {
        document.getElementById('order').style.color = "white";
      }
    }, 100);
  }

  resumenPedido(){
    document.getElementById('home').style.color = "white";
    document.getElementById('carta').style.color = "white";
    document.getElementById('sucursal').style.color = "white";
    document.getElementById('cart').style.color = "white";
    setTimeout(() => {
      document.getElementById('user').style.color = "white";
      document.getElementById('order').style.color = "white";
    }, 100);
  }

  sidebar(){
    setTimeout(() => {
      document.getElementById('menu-sidebar').className = "main-aside open";
    }, 100)
  }

  closeSidebar(){
    document.getElementById('menu-sidebar').className = "main-aside";
  }

  getSucursal(){
    const sucursal = JSON.parse(localStorage.getItem('sucursal'));
    if (sucursal === null) {
      this.getAllSucursal();
      var modal = document.getElementById("modal-sucursal");
      modal.style.display = "flex";
    } else {
      var modal = document.getElementById("modal-sucursal");
      modal.style.display = "none";
    }
  }

  getAllSucursal(){
    this.sucursalService.getSucursal().subscribe(
      (res) => {
        if (res.status) {
          this.sucursalArray = res.data;
          const sucursal = JSON.parse(localStorage.getItem('sucursal')).sucursal;
          this.selectSucursalCard(sucursal);
        }
      }
    )
  }

  openModalSucursal(){
    var modal = document.getElementById("modal-sucursal-seleccion");
    modal.style.display = "flex";
    
    this.getAllSucursal();
  }

  addSucursalStorage(){
    const opcion = confirm("Al confirmar el cambio de sucursal, se eliminarán todos sus cambios");
    if (opcion) {
      const json = {
        sucursal : this.sucursalIdData
      };
      localStorage.setItem('sucursal', JSON.stringify(json));
      var modal = document.getElementById("modal-sucursal");
      localStorage.removeItem('detailOrder');
      localStorage.removeItem('menuCheckout');
      window.location.reload();
    } else {}
    
  }

  closeSucursal() {
    var modal = document.getElementById("modal-sucursal-seleccion");
    modal.style.display = "none";
  }

  selectSucursalCard(id){
    setTimeout(() => {
      for(let i = 0; i < this.sucursalArray.length; i++) {
        let card = document.getElementById('card_select_' + this.sucursalArray[i].id);
        card.style.backgroundColor = "deepskyblue";
      }
      console.log('card_select_' + id);
      console.log(document.getElementById('card_select_' + id));
      let card = document.getElementById('card_select_' + id);
      card.style.backgroundColor = "#FF3399";
      this.sucursalIdData = id;
    }, 300);
  }

}
