import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { FoodService } from 'src/app/services/food.service';
import { MenuClass, MenuAddCheckoutClass } from 'src/app/models/menu.models';
import { StoresOpen } from 'src/app/models/stores.models';
import { ToastrService } from 'ngx-toastr';
import { ComplementService } from 'src/app/services/complement.service';
import { SucursalService } from 'src/app/services/sucursal.service';
import Swal from 'sweetalert2';
import { SettingService } from 'src/app/services/settings.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-menu-item-one',
  templateUrl: './menu-item-one.component.html',
  styleUrls: ['./menu-item-one.component.css']
})
export class MenuItemOneComponent implements OnInit {

  public menu = new MenuClass('', '', '', '', '', '', '', '', '', '', '', '');
  public storesHours = new StoresOpen(0);
  public menuCheckout = new MenuAddCheckoutClass(0, 0, '', []);
  public quantity = 1;
  public groupComplement = [];
  public addComplement = [];
  public validateAddComplement = [];
  public menuId = 0;
  public loading = false;
  public buttonValidate = false;
  public groupRequired = [];
  public groupRequiredBack = [];
  public groupsId = [];
  public opennig = true;
  public image = { detailCart : '' };
  public color = "";

  constructor(
    private router: Router,
    private _router: ActivatedRoute,
    private foodService: FoodService,
    private toastr: ToastrService,
    private complementService: ComplementService,
    private sucursalService : SucursalService,
    private settingService : SettingService
  ) { }

  ngOnInit(): void {
    this.loading = true;
    const detailCartStorage = JSON.parse(localStorage.getItem('bannerDetailCart'));
    if (detailCartStorage) {
      this.image.detailCart = detailCartStorage;
    } else {
      this.image.detailCart = `"assets/img/prods/1920x500/crepeduo.png"`;
    }
    document.getElementById("banner").style.backgroundImage = `url(${this.image.detailCart})`;
    this.getSettingsImage();
    const id: Observable<string> = this._router.params.pipe(map(p => p.menuId));
    //@ts-ignore
    const menuId = id.destination._value.menuId;
    this.menuId = parseInt(menuId);
    this.getMenuBy(menuId);
    this.getGroupComplement(menuId);
    this.validateHours();

    const setInterval = JSON.parse(localStorage.getItem('setIntervals'));
    clearInterval(setInterval);
    setTimeout(() => {
      localStorage.removeItem('setIntervals')
    }, 200);
  }

  getSettingsImage() {
    this.settingService.getImages().subscribe(
      (res) => {
        const resp = res.data;
        const detailCart = resp.find(e => e.id == 3);
        if (detailCart) {
          const detailCartURI = `${environment.store}/${detailCart.upload}`;
          localStorage.setItem('bannerDetailCart', JSON.stringify(detailCartURI));
          this.image.detailCart = detailCartURI;
        } else {
          localStorage.setItem('bannerDetailCart', `"assets/img/prods/1920x500/crepeduo.png"`);
          this.image.detailCart = `"assets/img/prods/1920x500/crepeduo.png"`;
        }
        document.getElementById("banner").style.backgroundImage = `url(${this.image.detailCart})`;
      }
    );
  }

  getMenuBy(menuId) {
    this.foodService.getMenuBy(menuId).subscribe(
      (res) => {
        if (res.status) {
          this.menu = res.data[0];
        }
      }
    );
  }

  validateHours () {
    this.storesHours.storeId = JSON.parse(localStorage.getItem('sucursal')).sucursal;
    this.sucursalService.validateSucursal().subscribe(
      (res) => {
        const message = res.data[0].message;
        if (message == 'OPEN') {
          this.opennig = true;
        } else {
          this.opennig = false;
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }

  less() {
    if (this.quantity > 1) {
      this.quantity = this.quantity - 1;
    }
  }

  more() {
    this.quantity = this.quantity + 1;
  }

  addCheckout(menu) {
    console.log(this.opennig);
    if (this.opennig) {
      this.menuCheckout.id = 1;
      this.menuCheckout.quantity = this.quantity;
      this.menuCheckout.menu = menu;
      this.menuCheckout.complement = this.addComplement;
      const validate = this.validateAddComplement.filter(e => e == false);
      if (validate[0] != false) {
        const menuCheckout = JSON.parse(localStorage.getItem('menuCheckout'));
        if (menuCheckout) {

          this.menuCheckout.id = menuCheckout.length + 1;

          // Menú diferente
          let menuStorageDifferent = menuCheckout.filter((e) => e.menu.id != menu.id);
          // Menú seleccionado
          let menuStorage = menuCheckout.filter((e) => e.menu.id == menu.id);

          menuStorage.push(this.menuCheckout);
          const newArray = menuStorage.concat(menuStorageDifferent);
          localStorage.setItem('menuCheckout', JSON.stringify(newArray));
          this.toastr.success('Añadido a la cesta');

          this.cleanOrder();

        } else {
          localStorage.setItem('menuCheckout', JSON.stringify([this.menuCheckout]));
          this.toastr.success('Agregado a la cesta');

          this.cleanOrder();
        }
      } else {
        this.toastr.info('Al parecer uno de los complementos no son los correctos');
      }
    } else {
      Swal.fire(
        'Opps...',
        'La tienda se encuentra cerrada',
        'info'
      )
    }
  }

  getGroupComplement(menuId) {
    this.complementService.getGroupComplement(menuId).subscribe(
      (res) => {
        if (res.status) {
          this.groupComplement = res.data;
          const groupRequired = [];
          for(let i = 0; i < this.groupComplement.length; i++) {
            const groupId = this.groupComplement[i].groupId;
            const countMin = this.groupComplement[i].cantMin;
            const countMax = this.groupComplement[i].cantMax;
            if (countMin != 0) {
              const json = { groupId : groupId, countMin : countMin, countMax : countMax };
              groupRequired.push(json);
            }
          }
          this.groupRequired = groupRequired;
          this.groupRequiredBack = groupRequired;

          if (groupRequired.length > 0) {
            this.buttonValidate = false;
          } else {
            this.buttonValidate = true;
          }
          setTimeout(() => {
            // this.loading = false;
            this.getColor();
          }, 1500);
        }
      }
    );
  }

  cleanOrder() {
    this.menu = new MenuClass('', '', '', '', '', '', '', '', '', '', '', '');
    this.menuCheckout = new MenuAddCheckoutClass(0, 0, '', []);
    this.quantity = 1;
    this.groupComplement = [];
    this.addComplement = [];
    this.validateAddComplement = [];
    this.groupRequired = [];
    this.groupRequiredBack = [];

    this.ngOnInit();
  }

  collapse(groupId) {
    const groupIdElement = document.getElementById('accordion_' + groupId);
    if (groupIdElement.style.display === "none") {
      groupIdElement.style.display = "block";
    } else {
      groupIdElement.style.display = "none";
    }
  }

  validateCheck(groupId, max) {
    this.addComplement = [];
    this.validateAddComplement = [];
    const complementeGroup = this.groupComplement;
    for (let i = 0; i < complementeGroup.length; i++) {
      const group = complementeGroup[i];
      const complement = group.complement;
      const groupId = group.groupId;
      const nameGroup = group.nameGroup;
      const min = group.cantMin;
      const max = group.cantMax;
      var countComplement = 0;
      var initComplement = false;
      const complementArray = [];
      for (let j = 0; j < complement.length; j++) {
        const complementId = complement[j].complementId;
        const nameComplement = complement[j].name;
        const priceComplement = complement[j].price;
        //@ts-ignore
        const validateChecked = document.getElementById('complement_' + complementId + '_' + groupId).checked;
        if (validateChecked) {
          initComplement = true;
          countComplement = countComplement + 1;
          const json = { 'complementId': complementId, 'name': nameComplement, 'price': priceComplement };
          complementArray.push(json);
        }
      }
      if (initComplement) {
        const jsonGroupComplement = {
          'groupId' : groupId,
          'name' : nameGroup,
          'min' : min,
          'max' : max,
          'complement' : complementArray
        };
        if (group.cantMin <= countComplement && countComplement <= group.cantMax) {
          this.validateAddComplement.push(true);
        } else {
          this.validateAddComplement.push(false);
        }
        this.addComplement.push(jsonGroupComplement);
      }
    }


    this.groupRequired = [];
    for ( let l = 0; l < this.groupRequiredBack.length; l++ ) {
      const groupIdC = this.groupRequiredBack[l].groupId;
      const countMin = this.groupRequiredBack[l].countMin;
      const countMax = this.groupRequiredBack[l].countMax;
      const complementsRequired = this.addComplement.filter((e) => e.groupId == groupIdC);
      if (complementsRequired.length > 0) {
        const groupComplementLength = complementsRequired[0].complement.length;
        if (groupComplementLength >= countMin ) {
        } else {
          const json = { groupId : groupIdC, countMin : countMin, countMax : countMax };
          this.groupRequired.push(json);
        }
      } else {
        const json = { groupId : groupIdC, countMin : countMin, countMax : countMax };
        this.groupRequired.push(json);
      }
    }

    /*if (this.groupRequired.length === 0) {
      this.buttonValidate = true;
    }*/

    const getAddComplement = this.addComplement.filter((e) => e.groupId == groupId);
    let length = 0;
    if (getAddComplement.length > 0) {
      length = getAddComplement[0].complement.length;
    }

    /** Validando del boton de agregar **/
    const groupRequiredFilter = this.groupRequiredBack.filter((i) => i.groupId == groupId);
    if (groupRequiredFilter[0]) {
      if (length > 0) {
        if (!this.groupsId.includes(groupId)) {
          this.groupsId.push(groupId);
        }
      }else {
        const item = this.groupsId.indexOf(groupId);
        this.groupsId.splice(item, 1);
      }
    }

    console.log(this.groupsId);
    if (this.groupsId.length === this.groupRequiredBack.length) {
      this.buttonValidate = true;
    } else {
      this.buttonValidate = false;
    }

    if (length >= max) {
      const groupsFilters = this.groupComplement.filter((e) => e.groupId == groupId);
      const complementsFilters = groupsFilters[0].complement;
      for (let i = 0; i < complementsFilters.length; i++) {
        const complementId = complementsFilters[i].complementId;
        //@ts-ignore
        const checked = document.getElementById('complement_' + complementId + '_' + groupId).checked;
        if (checked) {
          //@ts-ignore
          document.getElementById('complement_' + complementId + '_' + groupId).disabled = false;
        } else {
          //@ts-ignore
          document.getElementById('complement_' + complementId + '_' + groupId).disabled = true;
        }
      }
    } else {
      const groupsFilters = this.groupComplement.filter((e) => e.groupId == groupId);
      const complementsFilters = groupsFilters[0].complement;
      for (let i = 0; i < complementsFilters.length; i++) {
        const complementId = complementsFilters[i].complementId;
        //@ts-ignore
        const checked = document.getElementById('complement_' + complementId + '_' + groupId).checked;
        if (checked) {
          //@ts-ignore
          document.getElementById('complement_' + complementId + '_' + groupId).disabled = false;
        } else {
          //@ts-ignore
          document.getElementById('complement_' + complementId + '_' + groupId).disabled = false;
        }
      }
    }
  }

  recoverCheckout(checkout) {
    this.menuCheckout = checkout;
    for (let i = 0; i < this.menuCheckout.complement.length; i++) {
      const groupId = this.menuCheckout.complement[i].groupId;
      const complement = this.menuCheckout.complement[i].complement;
      document.getElementById('accordion_' + groupId).style.display = "block";
      for (let j = 0; j < complement.length; j++) {
        const complementId = complement[j].complementId;
        //@ts-ignore
        document.getElementById('complement_' + complementId + '_' + groupId).checked = true;
      }
    }
    this.quantity = this.menuCheckout.quantity;
  }

  getColor () {
    this.settingService.getSettings().subscribe(
      (res) => {
        this.color = "#f39";
        this.loading = false;
      }
    );
  }

}
